(function() {
  var DashboardCompanySelectboxCtrl;

  DashboardCompanySelectboxCtrl = ["$scope", "$state", "$stateParams", "$uibModal", "MnoeCurrentUser", "MnoeOrganizations", "MnoeAppInstances", "MnoeConfig", function($scope, $state, $stateParams, $uibModal, MnoeCurrentUser, MnoeOrganizations, MnoeAppInstances, MnoeConfig) {
    'ngInject';
    var selectBox, selectOrganization;
    $scope.selectBox = selectBox = {
      isClosed: true,
      isShown: false,
      user: MnoeCurrentUser.user,
      organization: '',
      isOrganizationManagementEnabled: MnoeConfig.isOrganizationManagementEnabled()
    };
    selectBox.changeTo = function(organization) {
      if (organization.id === MnoeOrganizations.selectedId) {
        return;
      }
      MnoeAppInstances.emptyAppInstances();
      MnoeAppInstances.clearCache();
      MnoeOrganizations.get(organization.id);
      return selectBox.close();
    };
    selectOrganization = function() {
      return selectBox.organization = _.find(MnoeCurrentUser.user.organizations, {
        id: MnoeOrganizations.selectedId
      });
    };
    selectBox.toggle = function() {
      return selectBox.isClosed = !selectBox.isClosed;
    };
    selectBox.close = function() {
      return selectBox.isClosed = true;
    };
    selectBox.openCreateCompanyModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/components/dashboard-company-selectbox/modals/create-company.html',
        controller: 'CreateCompanyModalCtrl',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static'
      });
      return modalInstance.result.then(function(organization) {
        selectBox.changeTo(organization);
        if (MnoeConfig.isOnboardingWizardEnabled()) {
          return $state.go('onboarding.step1');
        } else {
          return $state.go('home.impac');
        }
      });
    };
    return $scope.$watch(MnoeOrganizations.getSelectedId, function(val) {
      if (val != null) {
        return selectOrganization();
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardCompanySelectbox', function() {
    return {
      restrict: 'EA',
      controller: DashboardCompanySelectboxCtrl,
      templateUrl: 'app/components/dashboard-company-selectbox/dashboard-company-selectbox.html'
    };
  });

}).call(this);
