(function() {
  angular.module('mnoEnterpriseAngular').controller('ProvisioningDetailsCtrl', ["$state", "MnoeProvisioning", function($state, MnoeProvisioning) {
    var vm;
    vm = this;
    vm.form = ["*"];
    vm.subscription = MnoeProvisioning.getSubscription();
    vm.isEditMode = !_.isEmpty(vm.subscription.custom_data);
    MnoeProvisioning.findProduct({
      id: vm.subscription.product.id
    }).then(function(response) {
      return vm.schema = JSON.parse(response.custom_schema);
    });
    vm.submit = function(form) {
      if (form.$invalid) {
        return;
      }
      MnoeProvisioning.setSubscription(vm.subscription);
      return $state.go('home.provisioning.confirm');
    };
  }]);

}).call(this);
