(function() {
  var DashboardAppsDockCtrl;

  DashboardAppsDockCtrl = ["$scope", "$cookies", "$uibModal", "$window", "MnoeOrganizations", "MnoeAppInstances", "MnoeConfig", function($scope, $cookies, $uibModal, $window, MnoeOrganizations, MnoeAppInstances, MnoeConfig) {
    'ngInject';
    $scope.appDock = {};
    $scope.appDock.isMinimized = true;
    $scope.activeApp = null;
    $scope.launchApp = {
      isClosed: true
    };
    $scope.isMarketplaceEnabled = MnoeConfig.isMarketplaceEnabled();
    $scope.appDock.isAnimated = function() {
      return $scope.activeApp === null;
    };
    $scope.modal || ($scope.modal = {});
    $scope.helper = {};
    $scope.helper.displaySettings = function() {
      return MnoeOrganizations.can.update.appInstance();
    };
    $scope.helper.isLaunchHidden = function(app) {
      return app.status === 'terminating' || app.status === 'terminated' || $scope.helper.isOauthConnectBtnShown(app) || $scope.helper.isNewOfficeApp(app);
    };
    $scope.helper.isOauthConnectBtnShown = function(instance) {
      return instance.app_nid !== 'office-365' && instance.stack === 'connector' && !instance.oauth_keys_valid;
    };
    $scope.helper.isNewOfficeApp = function(instance) {
      return instance.stack === 'connector' && instance.appNid === 'office-365' && (moment(instance.createdAt) > moment().subtract({
        minutes: 5
      }));
    };
    $scope.helper.oAuthConnectPath = function(instance) {
      MnoeAppInstances.clearCache();
      return $window.location.href = "/mnoe/webhook/oauth/" + instance.uid + "/authorize";
    };
    $scope.launchAction = function(app, event) {
      $scope.setActiveApp(event, app.id);
      if (app.customInfoRequired) {
        return false;
      } else {
        $window.open("/mnoe/launch/" + app.uid, '_blank');
        return true;
      }
    };
    $scope.setActiveApp = function(event, app) {
      if ($scope.isActiveApp(app)) {
        return $scope.activeApp = null;
      } else {
        angular.element(event.target).off('mouseout');
        return $scope.activeApp = app;
      }
    };
    $scope.isActiveApp = function(app) {
      return $scope.activeApp === app;
    };
    $scope.appDock.toggle = function() {
      return $scope.appDock.isMinimized = !$scope.appDock.isMinimized;
    };
    $scope.showSettingsModal = function(app) {
      var modalInstance;
      return modalInstance = $uibModal.open({
        templateUrl: 'app/views/apps/modals/app-settings-modal.html',
        controller: 'DashboardAppSettingsModalCtrl',
        resolve: {
          app: function() {
            return app;
          }
        }
      });
    };
    $scope.showConnectModal = function(app) {
      var modalInfo, modalInstance;
      switch (app.app_nid) {
        case "xero":
          modalInfo = {
            template: "app/views/apps/modals/app-connect-modal-xero.html",
            controller: 'DashboardAppConnectXeroModalCtrl'
          };
          break;
        case "myob":
          modalInfo = {
            template: "app/views/apps/modals/app-connect-modal-myob.html",
            controller: 'DashboardAppConnectMyobModalCtrl'
          };
          break;
        default:
          $scope.helper.oAuthConnectPath(app);
      }
      return modalInstance = $uibModal.open({
        templateUrl: modalInfo.template,
        controller: modalInfo.controller,
        resolve: {
          app: function() {
            return app;
          }
        }
      });
    };
    $scope.$watch(MnoeOrganizations.getSelectedId, function(val) {
      if (val != null) {
        $scope.isLoading = true;
        return MnoeAppInstances.getAppInstances().then(function(response) {
          $scope.isLoading = false;
          return $scope.apps = response;
        });
      }
    });
    return $scope.displayDock = function() {
      return $scope.isMarketplaceEnabled || (($scope.apps != null) && $scope.apps.length > 0);
    };
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardAppsDock', ["$window", function($window) {
    return {
      scope: {
        visibleIfEmpty: '=?'
      },
      link: function(scope, element) {
        var navHeight, pageHeight;
        pageHeight = angular.element(window).height();
        navHeight = pageHeight * 0.7;
        angular.element('#mobile-dock').css({
          maxHeight: navHeight + 'px'
        });
        element.on('mouseover', function(e) {
          var li, prevLi;
          if (scope.activeApp === null) {
            if (e.target.src) {
              li = e.target.parentNode;
              prevLi = angular.element(li.previousElementSibling);
              if (prevLi) {
                prevLi.addClass("prev");
              }
              return angular.element(e.target).bind('mouseout mouseleave', function() {
                if (prevLi) {
                  return prevLi.removeClass("prev");
                }
              });
            }
          }
        });
        return angular.element($window).on('mouseup', function(event) {
          var $menu;
          if ((scope.activeApp != null) && scope.launchApp.isClosed) {
            $menu = $('li.active.dock-icon');
            if ($menu.has(event.target).length === 0 && !$menu.is(event.target)) {
              return scope.$apply(scope.activeApp = null, $('ul.dock:not(.animated) > li.prev').removeClass('prev'));
            }
          }
        });
      },
      restrict: 'EA',
      controller: DashboardAppsDockCtrl,
      templateUrl: 'app/components/dashboard-apps-dock/dashboard-apps-dock.html'
    };
  }]);

}).call(this);
