(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardCompanyCtrl', ["$scope", "MnoeOrganizations", "MnoeTeams", "MnoeConfig", function($scope, MnoeOrganizations, MnoeTeams, MnoeConfig) {
    var vm;
    vm = this;
    vm.isLoading = true;
    vm.payment_enabled = MnoeConfig.isPaymentEnabled();
    vm.initialize = function() {
      vm.isLoading = false;
      if (vm.isBillingShown()) {
        return vm.activeTab = 'billing';
      } else {
        return vm.activeTab = 'members';
      }
    };
    vm.isTabSetShown = function() {
      return !vm.isLoading && (MnoeOrganizations.role.isSuperAdmin() || MnoeOrganizations.role.isAdmin());
    };
    vm.isBillingShown = function() {
      return MnoeConfig.isBillingEnabled() && MnoeOrganizations.role.isSuperAdmin();
    };
    vm.isSettingsShown = function() {
      return MnoeOrganizations.role.isSuperAdmin();
    };
    vm.isAuditLogShown = function() {
      return MnoeConfig.isAuditLogEnabled() && MnoeOrganizations.role.isSuperAdmin();
    };
    $scope.$watch(MnoeOrganizations.getSelectedId, function(val) {
      if (val != null) {
        vm.isLoading = true;
        return MnoeTeams.getTeams(true);
      }
    });
    $scope.$watch(MnoeOrganizations.getSelected, function(val) {
      if (val != null) {
        return vm.initialize();
      }
    });
  }]);

}).call(this);
