(function() {
  angular.module('mnoEnterpriseAngular').controller('EditQuestionModalCtrl', ["$log", "$stateParams", "$uibModalInstance", "toastr", "Utilities", "MnoeMarketplace", "question", function($log, $stateParams, $uibModalInstance, toastr, Utilities, MnoeMarketplace, question) {
    var vm;
    vm = this;
    vm.modal = {
      model: {}
    };
    vm.appRating = question.rating;
    vm.commentMaxLenght = 500;
    vm.modal.model.description = question.description;
    vm.modal.model.title = 'mno_enterprise.templates.dashboard.marketplace.show.edit_question';
    vm.modal.model.placeholder = 'mno_enterprise.templates.dashboard.marketplace.show.question_placeholder';
    vm.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.modal.proceed = function() {
      var app_question;
      vm.modal.isLoading = true;
      app_question = {
        description: vm.modal.model.description
      };
      return MnoeMarketplace.editQuestion($stateParams.appId, question.id, app_question).then(function(response) {
        toastr.success('mno_enterprise.templates.dashboard.marketplace.show.success_toastr_2');
        return $uibModalInstance.close(response);
      }, function(errors) {
        $log.error(errors);
        toastr.error('mno_enterprise.templates.dashboard.marketplace.show.error_toastr');
        return Utilities.processRailsError(errors);
      })["finally"](function() {
        return vm.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
