(function() {
  angular.module('mnoEnterpriseAngular').controller('CreateAnswerModalCtrl', ["$log", "$stateParams", "$uibModalInstance", "toastr", "Utilities", "MnoeMarketplace", "MnoeOrganizations", "question", function($log, $stateParams, $uibModalInstance, toastr, Utilities, MnoeMarketplace, MnoeOrganizations, question) {
    var vm;
    vm = this;
    vm.modal = {
      model: {}
    };
    vm.commentMaxLenght = 500;
    vm.questionText = question.description;
    vm.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.modal.proceed = function() {
      var app_question;
      vm.modal.isLoading = true;
      app_question = {
        description: vm.modal.model.description,
        organization_id: MnoeOrganizations.getSelectedId(),
        question_id: question.id
      };
      return MnoeMarketplace.addAppQuestionAnswer($stateParams.appId, app_question).then(function(response) {
        toastr.success('mno_enterprise.templates.dashboard.marketplace.show.success_toastr_answer');
        return $uibModalInstance.close(response);
      }, function(errors) {
        $log.error(errors);
        toastr.error('mno_enterprise.templates.dashboard.marketplace.show.error_toastr_answer');
        return Utilities.processRailsError(errors);
      })["finally"](function() {
        return vm.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
