(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeAuditEvents', ["$log", "MnoeFullApiSvc", "MnoeOrganizations", function($log, MnoeFullApiSvc, MnoeOrganizations) {
    var _event_index, _self;
    _self = this;
    _event_index = function() {
      return MnoeFullApiSvc.one('organizations', MnoeOrganizations.selectedId).all("audit_events");
    };
    this.list = function(limit, offset, sort) {
      var params;
      params = {
        order_by: sort,
        limit: limit,
        offset: offset
      };
      return _event_index().getList(params);
    };
    this.exportUrl = function() {
      return _event_index().getRestangularUrl() + ".csv";
    };
    return this;
  }]);

}).call(this);
