(function() {
  angular.module('mnoEnterpriseAngular').controller('LayoutController', ["$scope", "$stateParams", "$state", "$q", "MnoeCurrentUser", "MnoeOrganizations", function($scope, $stateParams, $state, $q, MnoeCurrentUser, MnoeOrganizations) {
    'ngInject';
    $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue) {
      if (newValue != null) {
        return MnoeCurrentUser.get().then(function(response) {
          var selectedOrg;
          if ($state.is('home.impac') || $state.is('home.apps')) {
            selectedOrg = _.find(response.organizations, {
              id: newValue
            });
            if (MnoeOrganizations.role.atLeastAdmin(selectedOrg.current_user_role)) {
              return $state.go('home.impac');
            } else {
              return $state.go('home.apps');
            }
          }
        });
      }
    });
  }]);

}).call(this);
