(function() {
  angular.module('mnoEnterpriseAngular').controller('EditCommentModalCtrl', ["$log", "$stateParams", "$uibModalInstance", "toastr", "Utilities", "MnoeMarketplace", "object", function($log, $stateParams, $uibModalInstance, toastr, Utilities, MnoeMarketplace, object) {
    var vm;
    vm = this;
    vm.modal = {
      model: {}
    };
    vm.commentMaxLenght = 500;
    vm.modal.model.description = object.description;
    vm.modal.model.title = 'mno_enterprise.templates.dashboard.marketplace.show.edit_comment';
    vm.modal.model.placeholder = 'mno_enterprise.templates.dashboard.marketplace.show.comment_placeholder';
    vm.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.modal.proceed = function() {
      var app_payload;
      vm.modal.isLoading = true;
      app_payload = {
        description: vm.modal.model.description
      };
      return MnoeMarketplace.editComment($stateParams.appId, object.id, app_payload).then(function(response) {
        toastr.success('mno_enterprise.templates.dashboard.marketplace.show.success_toastr_2');
        return $uibModalInstance.close(response);
      }, function(errors) {
        $log.error(errors);
        toastr.error('mno_enterprise.templates.dashboard.marketplace.show.error_toastr');
        return Utilities.processRailsError(errors);
      })["finally"](function() {
        return vm.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
