(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardMarketplaceCompareCtrl', ["$scope", "$stateParams", "$state", "MnoeMarketplace", "MnoeConfig", function($scope, $stateParams, $state, MnoeMarketplace, MnoeConfig) {
    var currency, vm;
    vm = this;
    vm.isPriceShown = MnoeConfig.isMarketplacePricingEnabled();
    vm.isReviewingEnabled = MnoeConfig.areMarketplaceReviewsEnabled();
    vm.isLoading = true;
    currency = MnoeConfig.marketplaceCurrency();
    vm.pricing_plans = [currency] || 'AUD' || 'default';
    MnoeMarketplace.getApps().then(function(response) {
      response = response.plain();
      vm.comparedApps = _.each(_.filter(response.apps, function(app) {
        return app.toCompare === true;
      }), function(app) {
        app.average_rating = parseFloat(app.average_rating).toFixed(1);
        return true;
      });
      return vm.isLoading = false;
    });
  }]);

}).call(this);
