(function() {
  var DashboardOrganizationAuditLogCtrl;

  DashboardOrganizationAuditLogCtrl = ["$scope", "MnoeAuditEvents", function($scope, MnoeAuditEvents) {
    'ngInject';
    $scope.isLoading = true;
    return $scope.exportUrl = MnoeAuditEvents.exportUrl();
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationAuditLog', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/audit-log/organization-audit-log.html',
      controller: DashboardOrganizationAuditLogCtrl
    };
  });

}).call(this);
