(function() {
  angular.module('mnoEnterpriseAngular').service('MnoLocalStorage', ["$window", "$log", function($window, $log) {
    var _warning;
    _warning = function(key) {
      var messages;
      if (!key) {
        return;
      }
      messages = {
        parse_error: "cannot fromJson undefined values",
        setobject_error: "LocalStorage is disabled"
      };
      return $log.warn("LocalStorage Service Warning: " + messages[key]);
    };
    this.set = function(key, value) {
      var e;
      try {
        return $window.localStorage[key] = value;
      } catch (error) {
        e = error;
        return _warning('setobject_error');
      }
    };
    this.get = function(key, defaultValue) {
      ret;
      var e, ret;
      try {
        ret = angular.fromJson($window.localStorage[key]);
      } catch (error) {
        e = error;
        _warning('parse_error');
        ret = $window.localStorage[key];
      }
      return ret || defaultValue;
    };
    this.setObject = function(key, value) {
      var e;
      if (typeof value === 'undefined') {
        _warning('parse_error');
      }
      try {
        return $window.localStorage[key] = angular.toJson(value || {});
      } catch (error) {
        e = error;
        return _warning('setobject_error');
      }
    };
    this.getObject = function(key, defaultValue) {
      return angular.fromJson($window.localStorage[key]) || defaultValue;
    };
    this.getKeys = function() {
      var i, keys;
      keys = [];
      i = 0;
      while (i < $window.localStorage.length) {
        keys.push($window.localStorage.getItem($window.localStorage.key(i)));
        ++i;
      }
      return keys;
    };
    this.removeItem = function(key) {
      return $window.localStorage.removeItem(key);
    };
    this.clear = function() {
      return $window.localStorage.clear();
    };
  }]);

}).call(this);
