(function() {
  angular.module('mnoEnterpriseAngular').controller('OnboardingController', ["MnoeCurrentUser", function(MnoeCurrentUser) {
    'ngInject';
    var onboarding;
    onboarding = this;
    MnoeCurrentUser.get().then(function(response) {
      return onboarding.user = response;
    });
  }]);

}).call(this);
