(function() {
  angular.module('mnoEnterpriseAngular').controller('AppDeletionModalCtrl', ["$scope", "$uibModalInstance", "MnoeAppInstances", "Utilities", "app", function($scope, $uibModalInstance, MnoeAppInstances, Utilities, app) {
    return $scope.modal = {
      app: app,
      loading: false,
      sentence: 'Please proceed to the deletion of my app and all data it contains',
      deleteApp: function() {
        $scope.modal.loading = true;
        return MnoeAppInstances.terminate($scope.modal.app.id).then(function(success) {
          $scope.modal.errors = null;
          return $uibModalInstance.close();
        }, function(error) {
          return $scope.modal.errors = Utilities.processRailsError(error);
        })["finally"](function() {
          return $scope.modal.loading = false;
        });
      },
      cancel: function() {
        return $uibModalInstance.dismiss('cancel');
      }
    };
  }]);

}).call(this);
