(function() {
  angular.module('mnoEnterpriseAngular').service('MnoConfirm', ["$uibModal", "$q", function($uibModal, $q) {
    var _self, modalDefaults, modalOptions;
    _self = this;
    modalOptions = {
      closeButtonText: 'Close',
      actionButtonText: 'OK',
      headerText: 'Proceed?',
      bodyText: 'Perform this action?',
      actionCb: $q.resolve,
      type: 'primary'
    };
    modalDefaults = {
      backdrop: true,
      keyboard: true,
      modalFade: true,
      templateUrl: 'app/components/mno-confirm/mno-confirm.html'
    };
    this.showModal = function(customModalOptions, customModalDefaults) {
      if (customModalDefaults == null) {
        customModalDefaults = null;
      }
      if (customModalDefaults == null) {
        customModalDefaults = {};
      }
      customModalDefaults.backdrop = 'static';
      return _self.show(customModalOptions, customModalDefaults);
    };
    this.show = function(customModalOptions, customModalDefaults) {
      var tempModalDefaults, tempModalOptions;
      if (customModalDefaults == null) {
        customModalDefaults = null;
      }
      tempModalDefaults = {};
      tempModalOptions = {};
      angular.extend(tempModalOptions, modalOptions, customModalOptions);
      angular.extend(tempModalDefaults, modalDefaults, customModalDefaults);
      if (!tempModalDefaults.controller) {
        tempModalDefaults.controller = ["$scope", "$uibModalInstance", function($scope, $uibModalInstance) {
          'ngInject';
          $scope.modalOptions = tempModalOptions;
          $scope.modalOptions.ok = function() {
            $scope.modalOptions.isLoading = true;
            return $scope.modalOptions.actionCb().then(function(response) {
              return $uibModalInstance.close(response);
            })["finally"](function() {
              return $scope.modalOptions.isLoading = false;
            });
          };
          return $scope.modalOptions.close = function() {
            return $uibModalInstance.dismiss('cancel');
          };
        }];
      }
      return $uibModal.open(tempModalDefaults).result;
    };
    return this;
  }]);

}).call(this);
