(function() {
  angular.module('mnoEnterpriseAngular').config(["$logProvider", "toastrConfig", function($logProvider, toastrConfig) {
    'ngInject';
    $logProvider.debugEnabled(true);
    toastrConfig.timeOut = 5000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    return toastrConfig.progressBar = true;
  }]).config(["$httpProvider", function($httpProvider) {
    'ngInject';
    var token;
    $httpProvider.defaults.headers.common['Accept'] = 'application/json';
    $httpProvider.defaults.headers.common['Content-Type'] = 'application/json';
    if (token = $("meta[name='csrf-token']").attr("content")) {
      $httpProvider.defaults.headers.common['X-CSRF-Token'] = token;
    }
    return $httpProvider;
  }]).config(["$httpProvider", function($httpProvider) {
    return $httpProvider.interceptors.push(["$q", "$window", function($q, $window) {
      return {
        responseError: function(rejection) {
          if (rejection.status === 401) {
            console.log("User is not connected!");
            $window.location.href = '/';
          }
          return $q.reject(rejection);
        }
      };
    }]);
  }]).config(["$sceDelegateProvider", function($sceDelegateProvider) {
    'ngInject';
    return $sceDelegateProvider.resourceUrlWhitelist(['self']);
  }]).config(["$translateProvider", "LOCALES", function($translateProvider, LOCALES) {
    $translateProvider.useStaticFilesLoader({
      files: [
        {
          prefix: 'locales/',
          suffix: '.locale.json'
        }, {
          prefix: 'locales/impac/',
          suffix: '.json'
        }
      ]
    });
    $translateProvider.fallbackLanguage(LOCALES.fallbackLanguage);
    $translateProvider.useSanitizeValueStrategy('sanitizeParameters');
    return $translateProvider.useMessageFormatInterpolation();
  }]);

}).call(this);
