(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeCurrentUser', ["MnoeApiSvc", "$window", "$state", "URI", function(MnoeApiSvc, $window, $state, URI) {
    var _self, userPromise;
    _self = this;
    userPromise = null;
    this.user = {};
    this.get = function() {
      if (userPromise != null) {
        return userPromise;
      }
      return userPromise = MnoeApiSvc.one('current_user').get().then(function(response) {
        response = response.plain();
        if (!response.logged_in) {
          $window.location.href = URI.login;
        }
        angular.copy(response, _self.user);
        return response;
      });
    };
    this.refresh = function() {
      userPromise = null;
      return _self.get();
    };
    this.update = function(user) {
      return MnoeApiSvc.all('current_user').doPUT({
        user: user
      }).then(function(response) {
        angular.copy(response, _self.user);
        return response;
      });
    };
    this.registerDeveloper = function() {
      return MnoeApiSvc.all('/current_user').doPUT({}, '/register_developer').then(function(response) {
        return response.current_user;
      });
    };
    this.updatePassword = function(passwordData) {
      return MnoeApiSvc.all('/current_user').doPUT({
        user: passwordData
      }, 'update_password');
    };
    return this;
  }]);

}).call(this);
