(function() {
  angular.module('mnoEnterpriseAngular').directive('dashboardMenu', function() {
    return {
      restrict: 'EA',
      templateUrl: 'app/components/dashboard-menu/dashboard-menu.html',
      controller: ["$scope", "MnoeCurrentUser", "MnoeOrganizations", "MnoeConfig", function($scope, MnoeCurrentUser, MnoeOrganizations, MnoeConfig) {
        $scope.isDockEnabled = MnoeConfig.isDockEnabled();
        $scope.isOrganizationManagementEnabled = MnoeConfig.isOrganizationManagementEnabled();
        $scope.isMarketplaceEnabled = MnoeConfig.isMarketplaceEnabled();
        $scope.isProvisioningEnabled = MnoeConfig.isProvisioningEnabled();
        $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue) {
          if (newValue != null) {
            return MnoeCurrentUser.get().then(function(response) {
              var selectedOrg;
              selectedOrg = _.find(response.organizations, {
                id: newValue
              });
              return $scope.isAdmin = MnoeOrganizations.role.atLeastAdmin(selectedOrg.current_user_role);
            });
          }
        });
      }],
      link: function(scope, elem, attrs) {
        elem.find(".menu").on("mouseenter", function() {
          angular.element(this).stop();
          angular.element(this).find(".brand-logo").addClass('expanded');
          angular.element(this).find(".dashboard-button").find(".content").css("display", "block");
          return angular.element(this).animate({
            width: 275
          }, 150);
        });
        return elem.find(".menu").on("mouseleave", function() {
          angular.element(this).stop();
          angular.element(this).find(".brand-logo").removeClass('expanded');
          angular.element(this).find(".dashboard-button").find(".content").css("display", "none");
          return angular.element(this).animate({
            width: 80
          }, 150);
        });
      }
    };
  });

}).call(this);
