(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  angular.module('mnoEnterpriseAngular').component('mnoAppInstallBtn', {
    bindings: {
      app: '='
    },
    templateUrl: 'app/components/mno-app-install-btn/mno-app-install-btn.html',
    controller: ["$q", "$state", "$window", "$uibModal", "toastr", "MnoeMarketplace", "MnoeCurrentUser", "MnoeOrganizations", "MnoeAppInstances", function($q, $state, $window, $uibModal, toastr, MnoeMarketplace, MnoeCurrentUser, MnoeOrganizations, MnoeAppInstances) {
      var displayConnectToastr, displayLaunchToastr, openChooseOrgaModal, vm;
      vm = this;
      vm.appInstallationStatus = function() {
        if (vm.appInstance) {
          if (vm.app.multi_instantiable) {
            return "INSTALLABLE";
          } else {
            return MnoeAppInstances.installStatus(vm.appInstance);
          }
        } else {
          if (vm.conflictingApp) {
            return "CONFLICT";
          } else {
            return "INSTALLABLE";
          }
        }
      };
      vm.canProvisionApp = false;
      vm.provisionApp = function() {
        var purchasePromise;
        if (!vm.canProvisionApp) {
          return;
        }
        vm.isLoadingButton = true;
        if (MnoeOrganizations.role.atLeastAdmin(vm.user_role)) {
          purchasePromise = MnoeOrganizations.purchaseApp(vm.app, MnoeOrganizations.selectedId);
        } else {
          purchasePromise = openChooseOrgaModal().result;
        }
        return purchasePromise.then(function() {
          $state.go('home.impac');
          switch (vm.app.stack) {
            case 'cloud':
              return displayLaunchToastr(vm.app);
            case 'cube':
              return displayLaunchToastr(vm.app);
            case 'connector':
              if (vm.app.nid === 'office-365') {
                return displayLaunchToastr(vm.app);
              } else {
                return displayConnectToastr(vm.app);
              }
          }
        }).then(function() {
          return MnoeAppInstances.refreshAppInstances();
        })["finally"](function() {
          return vm.isLoadingButton = false;
        });
      };
      displayLaunchToastr = function(app) {
        return toastr.success('mno_enterprise.templates.components.app_install_btn.success_launch_notification_body', 'mno_enterprise.templates.components.app_install_btn.success_notification_title', {
          extraData: {
            name: app.name
          },
          timeout: 10000
        });
      };
      displayConnectToastr = function(app) {
        return toastr.success('mno_enterprise.templates.components.app_install_btn.success_connect_notification_body', 'mno_enterprise.templates.components.app_install_btn.success_notification_title', {
          extraData: {
            name: app.name
          },
          timeout: 10000
        });
      };
      openChooseOrgaModal = function() {
        return $uibModal.open({
          backdrop: 'static',
          templateUrl: 'app/views/marketplace/modals/choose-orga-modal.html',
          controller: 'MarketplaceChooseOrgaModalCtrl',
          resolve: {
            app: vm.app
          }
        });
      };
      vm.launchAppInstance = function() {
        $window.open("/mnoe/launch/" + vm.appInstance.uid, '_blank');
        return true;
      };
      vm.connectAppInstance = function() {
        var modalInfo;
        switch (vm.appInstance.app_nid) {
          case "xero":
            modalInfo = {
              template: "app/views/apps/modals/app-connect-modal-xero.html",
              controller: 'DashboardAppConnectXeroModalCtrl'
            };
            break;
          case "myob":
            modalInfo = {
              template: "app/views/apps/modals/app-connect-modal-myob.html",
              controller: 'DashboardAppConnectMyobModalCtrl'
            };
            break;
          default:
            vm.launchAppInstance();
        }
        return $uibModal.open({
          templateUrl: modalInfo.template,
          controller: modalInfo.controller,
          resolve: {
            app: vm.appInstance
          }
        });
      };
      vm.init = function() {
        return $q.all({
          marketplace: MnoeMarketplace.getApps(),
          appInstances: MnoeAppInstances.getAppInstances(),
          currentUser: MnoeCurrentUser.get()
        }).then(function(response) {
          var appInstances, apps, authorizedOrganizations, currentUser, installedApps, names, nids;
          apps = response.marketplace.apps;
          appInstances = response.appInstances;
          currentUser = response.currentUser;
          authorizedOrganizations = _.filter(currentUser.organizations, function(org) {
            return MnoeOrganizations.role.atLeastAdmin(org.current_user_role);
          });
          vm.canProvisionApp = !_.isEmpty(authorizedOrganizations);
          vm.appInstance = _.find(appInstances, {
            app_nid: vm.app.nid
          });
          nids = _.compact(_.map(appInstances, function(a) {
            return a.app_nid;
          }));
          installedApps = _.filter(apps, function(a) {
            var ref;
            return ref = a.nid, indexOf.call(nids, ref) >= 0;
          });
          if (vm.app.subcategories) {
            names = _.map(vm.app.subcategories, 'name');
            return vm.conflictingApp = _.find(installedApps, function(app) {
              return _.find(app.subcategories, function(subCategory) {
                var ref;
                return !subCategory.multi_instantiable && (ref = subCategory.name, indexOf.call(names, ref) >= 0);
              });
            });
          }
        });
      };
      vm.init();
    }]
  });

}).call(this);
