(function() {
  angular.module('mnoEnterpriseAngular').service('AnalyticsSvc', ["MnoeCurrentUser", "INTERCOM_ID", "$window", function(MnoeCurrentUser, INTERCOM_ID, $window) {
    this.init = function(userData) {
      if ($window.Intercom) {
        return MnoeCurrentUser.get().then(function(response) {
          userData = {
            user_id: response.id,
            app_id: INTERCOM_ID,
            name: response.name,
            surname: response.surname,
            company: response.company,
            email: response.email,
            created_at: response.created_at
          };
          if (response.user_hash) {
            userData.user_hash = response.user_hash;
          }
          return $window.Intercom('boot', userData);
        });
      }
    };
    this.update = function() {
      if ($window.Intercom) {
        return $window.Intercom('update');
      }
    };
    this.logOut = function() {
      if ($window.Intercom) {
        return $window.Intercom('shutdown');
      }
    };
    return this;
  }]);

}).call(this);
