(function() {
  angular.module('mnoEnterpriseAngular').directive('mnoPassword', ["MnoErrorsHandler", function(MnoErrorsHandler) {
    return {
      restrict: 'E',
      scope: {
        form: '=',
        data: '=',
        confirm: '=?',
        current: '=?',
        currentText: '@',
        newText: '@',
        confirmText: '@'
      },
      templateUrl: 'app/components/mno-password/mno-password.html',
      link: function(scope, element, attrs) {
        if (!scope.current) {
          scope.current = false;
        }
        if (!scope.confirm) {
          scope.confirm = false;
        }
        scope.errorHandler = MnoErrorsHandler;
        return scope.password = {
          hasEightChars: false,
          hasOneNumber: false,
          hasOneUpper: false,
          hasOneLower: false,
          isSamePassword: false,
          check: function() {
            var passwordValidity;
            scope.password.resetServerErrors();
            scope.password.hasEightChars = (scope.data.password != null) && (scope.data.password.length >= 8);
            scope.password.hasOneNumber = false;
            scope.password.hasOneUpper = false;
            scope.password.hasOneLower = false;
            if (angular.isString(scope.data.password)) {
              _.forEach(scope.data.password.split(""), function(letter) {
                if (Number.isInteger(parseInt(letter))) {
                  scope.password.hasOneNumber = true;
                }
                if (letter === letter.toUpperCase() && letter !== letter.toLowerCase() && !parseInt(letter)) {
                  scope.password.hasOneUpper = true;
                }
                if (letter === letter.toLowerCase() && letter !== letter.toUpperCase() && !parseInt(letter)) {
                  return scope.password.hasOneLower = true;
                }
              });
              passwordValidity = scope.password.hasEightChars && scope.password.hasOneNumber && scope.password.hasOneUpper && scope.password.hasOneLower;
              scope.form.password.$setValidity("password", passwordValidity);
            }
            if (scope.confirm) {
              scope.password.isSamePassword = !!scope.data.password && !!scope.data.password_confirmation && scope.data.password === scope.data.password_confirmation;
              return scope.form.password_confirmation.$setValidity("password_confirmation", scope.password.isSamePassword);
            }
          },
          resetServerErrors: function() {
            return MnoErrorsHandler.resetErrors(scope.form);
          }
        };
      }
    };
  }]);

}).call(this);
