(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardAppConnectXeroModalCtrl', ["$scope", "$window", "$httpParamSerializer", "$uibModalInstance", "MnoeAppInstances", "app", function($scope, $window, $httpParamSerializer, $uibModalInstance, MnoeAppInstances, app) {
    $scope.app = app;
    $scope.path = MnoeAppInstances.oAuthConnectPath(app);
    $scope.form = {
      perform: true,
      xero_country: "AU"
    };
    $scope.countries = [
      {
        name: "Australia",
        value: "AU"
      }, {
        name: "USA",
        value: "US"
      }
    ];
    $scope.connect = function(form) {
      if ($scope.payroll) {
        form['extra_params[]'] = "payroll";
      }
      return $window.location.href = $scope.path + $httpParamSerializer(form);
    };
    $scope.close = function() {
      return $uibModalInstance.close();
    };
  }]);

}).call(this);
