(function() {
  angular.module('mnoEnterpriseAngular').config(["ImpacRoutesProvider", "IMPAC_CONFIG", function(ImpacRoutesProvider, IMPAC_CONFIG) {
    var data, mnoHub;
    mnoHub = IMPAC_CONFIG.paths.mnohub_api;
    data = {
      mnoHub: mnoHub,
      impacApi: IMPAC_CONFIG.protocol + "://" + IMPAC_CONFIG.host + "/api",
      kpis: {
        index: mnoHub + "/impac/kpis"
      }
    };
    return ImpacRoutesProvider.configureRoutes(data);
  }]).config(["ImpacThemingProvider", function(ImpacThemingProvider) {
    var options;
    options = {
      dataNotFoundConfig: {
        linkUrl: '#!/marketplace',
        linkTarget: '_self'
      },
      dhbErrorsConfig: {
        firstTimeCreated: {
          note: ''
        }
      },
      dhbSelectorConfig: {
        pdfModeEnabled: true
      },
      dhbKpisConfig: {
        enableKpis: true
      },
      alertsConfig: {
        enableAlerts: true
      }
    };
    return ImpacThemingProvider.configure(options);
  }]).config(["ImpacAssetsProvider", function(ImpacAssetsProvider) {
    var options;
    options = {
      defaultImagesPath: '/dashboard/images'
    };
    return ImpacAssetsProvider.configure(options);
  }]).run(["ImpacLinking", "ImpacConfigSvc", "IMPAC_CONFIG", function(ImpacLinking, ImpacConfigSvc, IMPAC_CONFIG) {
    var data;
    data = {
      user: ImpacConfigSvc.getUserData,
      organizations: ImpacConfigSvc.getOrganizations
    };
    if ((IMPAC_CONFIG.pusher != null) && (IMPAC_CONFIG.pusher.key != null)) {
      data.pusher_key = IMPAC_CONFIG.pusher.key;
    }
    return ImpacLinking.linkData(data);
  }]);

}).call(this);
