(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardMarketplaceCtrl', ["toastr", "MnoeMarketplace", "MnoeConfig", function(toastr, MnoeMarketplace, MnoeConfig) {
    var vm;
    vm = this;
    vm.isLoading = true;
    vm.selectedCategory = '';
    vm.searchTerm = '';
    vm.isMarketplaceCompare = MnoeConfig.isMarketplaceComparisonEnabled();
    vm.showCompare = false;
    vm.nbAppsToCompare = 0;
    vm.linkFor = function(app) {
      return "#/marketplace/" + app.id;
    };
    vm.appsFilter = function(app) {
      if (((vm.searchTerm != null) && vm.searchTerm.length > 0) || !vm.selectedCategory) {
        return true;
      } else {
        return _.contains(app.categories, vm.selectedCategory);
      }
    };
    vm.cancelComparison = function() {
      return vm.showCompare = false;
    };
    vm.uncheckAllApps = function() {
      return angular.forEach(vm.apps, function(items) {
        return items.toCompare = false;
      });
    };
    vm.enableComparison = function() {
      vm.nbAppsToCompare = 0;
      vm.uncheckAllApps();
      return vm.showCompare = true;
    };
    vm.toggleAppToCompare = function(app) {
      var selected;
      selected = app.toCompare;
      if (selected && vm.nbAppsToCompare >= 4) {
        toastr.info("mno_enterprise.templates.dashboard.marketplace.index.toastr.error");
        return app.toCompare = false;
      } else {
        return vm.nbAppsToCompare += selected ? 1 : -1;
      }
    };
    vm.canBeCompared = function() {
      return vm.nbAppsToCompare <= 4 && vm.nbAppsToCompare >= 2;
    };
    MnoeMarketplace.getApps().then(function(response) {
      response = response.plain();
      vm.categories = response.categories;
      vm.apps = response.apps;
      return vm.isLoading = false;
    });
  }]);

}).call(this);
