(function() {
  var answerMatch, questionMatch;

  angular.module('mnoEnterpriseAngular').filter("filterQuestions", function() {
    return function(questions, keyword) {
      var result;
      result = {};
      keyword = keyword.toLowerCase();
      angular.forEach(questions, function(question, key) {
        if (questionMatch(question, keyword) || answerMatch(question, keyword)) {
          return result[key] = question;
        }
      });
      return result;
    };
  }, questionMatch = function(question, keyword) {
    return question.description.toLowerCase().includes(keyword);
  }, answerMatch = function(question, keyword) {
    var result;
    result = false;
    _.each(question.answers, function(answer) {
      if (answer.description.toLowerCase().includes(keyword)) {
        result = true;
      }
      return true;
    });
    return result;
  });

}).call(this);
