(function() {
  angular.module('mnoEnterpriseAngular').directive('dashboardLanguageSelectbox', function() {
    return {
      restrict: 'EA',
      template: '<span class="selector-line form-inline form-group form-group-sm">\n  <label class="control-label" for="language-select">Language</label>\n  <select id="language-select" class="form-control" ng-model="selectedLangKey" ng-change="changeLanguage()" ng-options="locale.id as locale.name for locale in locales">\n  </select>\n</span>',
      controller: ["$scope", "$translate", "$window", "$uibModal", "MnoConfirm", "LOCALES", "URI", function($scope, $translate, $window, $uibModal, MnoConfirm, LOCALES, URI) {
        $scope.locales = LOCALES.locales;
        $scope.selectedLangKey = $translate.use();
        $scope.$watch('selectedLangKey', function(newVal, oldVal) {
          return $scope.previousLocale = oldVal;
        });
        return $scope.changeLanguage = function() {
          var locale, modalOptions;
          locale = _.findWhere(LOCALES.locales, {
            id: $scope.selectedLangKey
          });
          modalOptions = {
            closeButtonText: 'Cancel',
            actionButtonText: 'Change language',
            headerText: "Change language to " + locale.name + "?",
            bodyText: "Are you sure you want to change language to " + locale.name + "?"
          };
          return MnoConfirm.showModal(modalOptions).then(function() {
            $translate.use($scope.selectedLangKey);
            return $window.location.href = "/" + $scope.selectedLangKey + URI.dashboard;
          }, function() {
            return $scope.selectedLangKey = $scope.previousLocale;
          });
        };
      }]
    };
  });

}).call(this);
