(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardAccountCtrl', ["$log", "$timeout", "toastr", "MnoeCurrentUser", "MnoErrorsHandler", "Miscellaneous", "Utilities", "I18N_CONFIG", "MnoeConfig", function($log, $timeout, toastr, MnoeCurrentUser, MnoErrorsHandler, Miscellaneous, Utilities, I18N_CONFIG, MnoeConfig) {
    var userOrig, vm;
    vm = this;
    vm.countryCodes = Miscellaneous.countryCodes;
    vm.errors = {};
    vm.success = {};
    vm.isDeveloperSectionEnabled = MnoeConfig.isDeveloperSectionEnabled();
    vm.isUserManagementEnabled = MnoeConfig.isUserManagementEnabled();
    vm.user = {
      model: {},
      password: {},
      loading: false
    };
    userOrig = null;
    $timeout((function() {
      return vm.isPersoInfoOpen = true;
    }), 250);
    vm.user.hasChanged = function() {
      return !(_.isEqual(vm.user.model, userOrig));
    };
    vm.user.cancelChanges = function() {
      return vm.user.model = _.clone(userOrig);
    };
    vm.user.update = function() {
      vm.user.loading = true;
      return MnoeCurrentUser.update(vm.user.model).then(function(userResp) {
        var displayEmail;
        if (userOrig.email === vm.user.model.email) {
          vm.success.user = "Saved!";
        } else {
          vm.success.user = "Saved! A confirmation email will be sent to your new email address.\nYou will need to click on the link enclosed in this email in order to validate this new address.";
        }
        displayEmail = vm.user.model.email;
        vm.user.model = _.clone(userResp);
        vm.user.model.email = displayEmail;
        userOrig = _.clone(vm.user.model);
        return vm.user.loading = false;
      }, function(error) {
        vm.user.loading = false;
        return vm.errors.user = Utilities.processRailsError(error);
      });
    };
    vm.isChangePasswordOpen = false;
    vm.user.cancelPassword = function() {
      return vm.user.password = {};
    };
    vm.user.cancelPasswordEnabled = function() {
      return vm.user.password.current_password || vm.user.password.password || vm.user.password.password_confirmation;
    };
    vm.user.updatePassword = function(form) {
      vm.user.loading = true;
      MnoErrorsHandler.resetErrors(form);
      return MnoeCurrentUser.updatePassword(vm.user.password).then(function() {
        toastr.success('Your password has been changed!');
        return vm.user.cancelPassword();
      }, function(error) {
        toastr.error('An error occured, your password has not been changed.');
        $log.error('Error while updating user: ', error);
        return MnoErrorsHandler.processServerError(error, form);
      })["finally"](function() {
        return vm.user.loading = false;
      });
    };
    vm.isLocalizationVisible = I18N_CONFIG.enabled;
    vm.isLocalizationOpen = false;
    vm.isDevOpen = false;
    vm.user.registerDeveloper = function() {
      return MnoeCurrentUser.registerDeveloper().then(function(success) {
        return angular.extend(vm.user.model, success);
      });
    };
    vm.isSecretKeyRevealed = false;
    vm.user.switchSecretKey = function() {
      return vm.isSecretKeyRevealed = !vm.isSecretKeyRevealed;
    };
    MnoeCurrentUser.get().then(function(response) {
      var user;
      vm.user.model = user = _.clone(MnoeCurrentUser.user);
      userOrig = _.clone(user);
      if (user.deletion_request) {
        return vm.user.currentDeletionRequestToken = user.deletion_request.token;
      } else {
        return vm.user.currentDeletionRequestToken = -1;
      }
    });
  }]);

}).call(this);
