(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeTeams', ["MnoeApiSvc", "MnoeOrganizations", function(MnoeApiSvc, MnoeOrganizations) {
    var _self, teamsPromise;
    _self = this;
    this.teams = [];
    teamsPromise = null;
    this.getTeams = function(force) {
      if (force == null) {
        force = false;
      }
      if (teamsPromise !== null && !force) {
        return teamsPromise;
      }
      return teamsPromise = MnoeApiSvc.one('organizations', MnoeOrganizations.selectedId).one('teams').getList().then(function(response) {
        _self.teams = response.plain();
        return _self.teams;
      });
    };
    this.addTeam = function(team) {
      var payload;
      payload = {
        team: team
      };
      return MnoeApiSvc.one('organizations', MnoeOrganizations.selectedId).post('teams', payload).then(function(response) {
        team = response.plain();
        _self.teams.push(team);
        return team;
      });
    };
    this.updateTeamName = function(team) {
      var payload;
      payload = {
        team: _.pick(team, "name")
      };
      return MnoeApiSvc.one('teams', team.id).patch(payload).then(function(response) {
        var listTeam, newTeam;
        newTeam = response.plain().team;
        listTeam = _.find(_self.teams, {
          id: team.id
        });
        angular.copy(newTeam, listTeam);
        return response;
      });
    };
    this.updateTeamAppInstances = function(team, appInstances) {
      var payload;
      payload = {
        team: {
          app_instances: appInstances
        }
      };
      return MnoeApiSvc.one('teams', team.id).patch(payload).then(function(response) {
        var listTeam, newTeam;
        newTeam = response.plain().team;
        listTeam = _.find(_self.teams, {
          id: team.id
        });
        angular.copy(newTeam, listTeam);
        return response;
      });
    };
    this.deleteTeam = function(teamId) {
      return MnoeApiSvc.one('teams', teamId).remove().then(function(response) {
        _.remove(_self.teams, {
          id: teamId
        });
        return response;
      });
    };
    this.addUsers = function(teamId, users) {
      var payload;
      payload = {
        team: {
          users: users
        }
      };
      return MnoeApiSvc.one('teams', teamId).customPUT(payload, '/add_users').then(function(response) {
        response = response.plain();
        return response.team.users;
      });
    };
    this.removeUser = function(teamId, user) {
      var payload;
      payload = {
        team: {
          users: [user]
        }
      };
      return MnoeApiSvc.one('teams', teamId).customPUT(payload, '/remove_users').then(function(response) {
        response = response.plain();
        return response.team.users;
      });
    };
    return this;
  }]);

}).call(this);
