(function() {
  angular.module('mnoEnterpriseAngular').controller('OnboardingStep1Controller', ["MnoeCurrentUser", function(MnoeCurrentUser) {
    'ngInject';
    var vm;
    vm = this;
    vm.isLoading = true;
    MnoeCurrentUser.get().then(function(response) {
      return vm.user = response;
    })["finally"](function() {
      return vm.isLoading = false;
    });
  }]);

}).call(this);
