(function() {
  angular.module('mnoEnterpriseAngular').service('ImpacConfigSvc', ["$log", "$q", "MnoeCurrentUser", "MnoeOrganizations", function($log, $q, MnoeCurrentUser, MnoeOrganizations) {
    this.getUserData = function() {
      return MnoeCurrentUser.get();
    };
    this.getOrganizations = function() {
      var currentOrgIdPromise, userOrgsPromise;
      userOrgsPromise = MnoeCurrentUser.get().then(function() {
        var err, userOrgs;
        userOrgs = MnoeCurrentUser.user.organizations;
        if (!userOrgs) {
          $log.error(err = {
            msg: "Unable to retrieve user organizations"
          });
          return $q.reject(err);
        }
        return userOrgs;
      });
      currentOrgIdPromise = MnoeOrganizations.get(MnoeOrganizations.selectedId).then(function() {
        var currentOrgId, err;
        currentOrgId = MnoeOrganizations.selectedId;
        if (!currentOrgId) {
          $log.error(err = {
            msg: "Unable to retrieve current organization"
          });
          return $q.reject(err);
        }
        return currentOrgId;
      });
      return $q.all([userOrgsPromise, currentOrgIdPromise]).then(function(responses) {
        return {
          organizations: responses[0],
          currentOrgId: responses[1]
        };
      });
    };
    return this;
  }]);

}).call(this);
