(function() {
  angular.module('mnoEnterpriseAngular').directive('mnoLoader', function() {
    return {
      scope: {
        withBackground: '='
      },
      restrict: 'AE',
      template: '<div class="loading" ng-class="{ \'with-background\': withBackground }">\n  <div ng-show="withBackground" class="background-layer"></div>\n  <div class="fa-wrapper">\n    <i class="loader-icon"></i>\n  </div>\n</div>'
    };
  });

}).call(this);
