(function() {
  angular.module('mnoEnterpriseAngular').controller('CreateCompanyModalCtrl', ["$scope", "$uibModalInstance", "Utilities", "MnoeOrganizations", function($scope, $uibModalInstance, Utilities, MnoeOrganizations) {
    $scope.modal = {
      model: {}
    };
    $scope.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    return $scope.modal.proceed = function() {
      var data;
      $scope.modal.isLoading = true;
      data = {
        organization: $scope.modal.model
      };
      return MnoeOrganizations.create(data).then(function(response) {
        delete $scope.modal.errors;
        return $uibModalInstance.close(response);
      }, function(errors) {
        return $scope.modal.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return $scope.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
