(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardAppSettingsModalCtrl', ["$scope", "MnoConfirm", "MnoeOrganizations", "$uibModalInstance", "MnoeAppInstances", "Utilities", "app", "$window", "ImpacMainSvc", function($scope, MnoConfirm, MnoeOrganizations, $uibModalInstance, MnoeAppInstances, Utilities, app, $window, ImpacMainSvc) {
    $scope.modal || ($scope.modal = {});
    $scope.app = app;
    $scope.sentence = "Please proceed to the deletion of my app and all data it contains";
    $scope.organization_uid = ImpacMainSvc.config.currentOrganization.uid;
    $scope.helper = {};
    $scope.helper.canDeleteApp = function() {
      return MnoeOrganizations.can.destroy.appInstance();
    };
    $scope.modal.close = function() {
      return $uibModalInstance.close();
    };
    $scope.deleteApp = function() {
      $scope.modal.loading = true;
      return MnoeAppInstances.terminate($scope.app.id).then(function() {
        $scope.modal.errors = null;
        return $uibModalInstance.close();
      }, function(error) {
        return $scope.modal.errors = Utilities.processRailsError(error);
      })["finally"](function() {
        return $scope.modal.loading = false;
      });
    };
    $scope.helper.isDataSyncShown = function(app) {
      return app.stack === 'connector' && app.oauth_keys_valid;
    };
    $scope.helper.isDataDisconnectShown = function(app) {
      return app.stack === 'connector' && app.oauth_keys_valid;
    };
    $scope.helper.dataSyncPath = function(app) {
      return "/mnoe/webhook/oauth/" + app.uid + "/sync";
    };
    $scope.helper.companyName = function(app) {
      if (app.stack === 'connector' && app.oauth_keys_valid && app.oauth_company_name) {
        return app.oauth_company_name;
      }
      return false;
    };
    $scope.helper.isAddOnSettingShown = function(app) {
      return app.add_on;
    };
    $scope.helper.addOnSettingLauch = function(app) {
      $window.open("/mnoe/launch/" + app.uid + "?settings=true", '_blank');
      return true;
    };
    return $scope.helper.dataDisconnectClick = function(app) {
      var modalOptions;
      modalOptions = {
        closeButtonText: 'Cancel',
        actionButtonText: 'Disconnect app',
        headerText: "Disconnect " + app.app_name + "?",
        bodyText: "Are you sure you want to disconnect " + app.app_name + " and Maestrano?"
      };
      return MnoConfirm.showModal(modalOptions).then(function() {
        MnoeAppInstances.clearCache();
        return $window.location.href = "/mnoe/webhook/oauth/" + app.uid + "/disconnect";
      });
    };
  }]);

}).call(this);
