(function() {
  var DashboardOrganizationSettingsCtrl;

  DashboardOrganizationSettingsCtrl = ["$scope", "$window", "MnoeOrganizations", "Utilities", function($scope, $window, MnoeOrganizations, Utilities) {
    'ngInject';
    $scope.isLoading = true;
    $scope.model = {};
    $scope.origModel = {};
    $scope.forms = {};
    $scope.initialize = function(organization) {
      angular.copy(organization, $scope.model);
      angular.copy(organization, $scope.origModel);
      return $scope.isLoading = false;
    };
    $scope.save = function() {
      $scope.isLoading = true;
      return MnoeOrganizations.update($scope.model).then(function(response) {
        $scope.errors = '';
        angular.copy(response.organization, $scope.model);
        return angular.copy(response.organization, $scope.origModel);
      }, function(errors) {
        return $scope.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return $scope.isLoading = false;
      });
    };
    $scope.cancel = function() {
      angular.copy($scope.origModel, $scope.model);
      return $scope.errors = '';
    };
    $scope.isChanged = function() {
      return !angular.equals($scope.model, $scope.origModel);
    };
    $scope.isCancelShown = function() {
      return $scope.isChanged();
    };
    $scope.isSaveEnabled = function() {
      var f;
      f = $scope.forms;
      return $scope.isChanged() && f.settings.$valid;
    };
    return $scope.$watch(MnoeOrganizations.getSelected, function(newValue) {
      $scope.isLoading = true;
      if (newValue != null) {
        return $scope.initialize(MnoeOrganizations.selected.organization);
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationSettings', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/settings/organization-settings.html',
      controller: DashboardOrganizationSettingsCtrl
    };
  });

}).call(this);
