(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardAppsListCtrl', ["$scope", "$interval", "$q", "$stateParams", "$window", "$uibModal", "MnoConfirm", "MnoeOrganizations", "MnoeAppInstances", "MnoeConfig", function($scope, $interval, $q, $stateParams, $window, $uibModal, MnoConfirm, MnoeOrganizations, MnoeAppInstances, MnoeConfig) {
    $scope.blink = {
      value: 'neutral'
    };
    $scope.loading = true;
    $scope.originalApps = [];
    $scope.apps = [];
    $scope.isLoading = true;
    $scope.displayOptions = {};
    $scope.isMarketplaceEnabled = MnoeConfig.isMarketplaceEnabled();
    $scope.helper = {};
    $scope.helper.displayCogwheel = function() {
      return MnoeOrganizations.can.update.appInstance();
    };
    $scope.helper.canConnectApp = function() {
      return MnoeOrganizations.can.connect.appInstance();
    };
    $scope.helper.canRenameApp = function() {
      return MnoeOrganizations.can.update.appInstance();
    };
    $scope.helper.canDeleteApp = function() {
      return MnoeOrganizations.can.destroy.appInstance();
    };
    $scope.helper.isDataSyncShown = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    $scope.helper.dataSyncPath = function(instance) {
      return "/mnoe/webhook/oauth/" + instance.uid + "/sync";
    };
    $scope.helper.isDataDisconnectShown = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    $scope.helper.dataDisconnectClick = function(instance) {
      var modalOptions;
      modalOptions = {
        closeButtonText: 'Cancel',
        actionButtonText: 'Disconnect app',
        headerText: "Disconnect " + instance.app_name + "?",
        bodyText: "Are you sure you want to disconnect " + instance.app_name + " and Maestrano?"
      };
      return MnoConfirm.showModal(modalOptions).then(function() {
        MnoeAppInstances.clearCache();
        return $window.location.href = "/mnoe/webhook/oauth/" + instance.uid + "/disconnect";
      });
    };
    $scope.helper.appActionUrl = function(instance) {
      return "/mnoe/launch/" + instance.uid;
    };
    $scope.helper.companyName = function(instance) {
      if (instance.stack === 'connector' && instance.oauth_keys_valid && instance.oauth_company_name) {
        return instance.oauth_company_name;
      }
      return false;
    };
    $scope.helper.connectorVersion = function(instance) {
      if (instance.stack === 'connector' && instance.oauth_keys_valid && instance.connectorVersion) {
        return capitalize(instance.connectorVersion);
      }
      return false;
    };
    $scope.helper.isOauthConnectBtnShown = function(instance) {
      return instance.app_nid !== 'office-365' && instance.stack === 'connector' && !instance.oauth_keys_valid;
    };
    $scope.helper.oAuthConnectPath = function(instance) {
      MnoeAppInstances.clearCache();
      return $window.location.href = "/mnoe/webhook/oauth/" + instance.uid + "/authorize";
    };
    $scope.helper.isLaunchHidden = function(instance) {
      return instance.status === 'terminating' || instance.status === 'terminated' || $scope.helper.isOauthConnectBtnShown(instance) || $scope.helper.isNewOfficeApp(instance);
    };
    $scope.helper.isNewOfficeApp = function(instance) {
      return instance.stack === 'connector' && instance.appNid === 'office-365' && (moment(instance.createdAt) > moment().subtract({
        minutes: 5
      }));
    };
    $scope.openAppDeletionModal = function(app) {
      var modalInstance;
      return modalInstance = $uibModal.open({
        templateUrl: 'app/views/apps/modals/app-deletion-modal.html',
        controller: 'AppDeletionModalCtrl',
        resolve: {
          app: function() {
            return app;
          }
        }
      });
    };
    return $scope.$watch(MnoeOrganizations.getSelectedId, function(val) {
      if (val != null) {
        $scope.isLoading = true;
        return MnoeAppInstances.getAppInstances().then(function() {
          $scope.isLoading = false;
          return $scope.apps = MnoeAppInstances.appInstances;
        });
      }
    });
  }]);

}).call(this);
