(function() {
  var DashboardOrganizationTeamsCtrl;

  DashboardOrganizationTeamsCtrl = ["$scope", "$window", "$uibModal", "$q", "MnoeOrganizations", "MnoeTeams", "MnoeAppInstances", "Utilities", function($scope, $window, $uibModal, $q, MnoeOrganizations, MnoeTeams, MnoeAppInstances, Utilities) {
    'ngInject';
    var addTeamModal, matrix, teamDeletionModal;
    $scope.isLoading = true;
    $scope.teams = [];
    $scope.originalTeams = [];
    $scope.appInstances = [];
    $scope.initialize = function(teams, appInstances) {
      var realAppInstances;
      angular.copy(teams, $scope.teams);
      angular.copy(teams, $scope.originalTeams);
      realAppInstances = _.filter(appInstances, function(i) {
        return i.status !== 'terminated';
      });
      angular.copy(realAppInstances, $scope.appInstances);
      return $scope.isLoading = false;
    };
    $scope.isTeamEmpty = function(team) {
      return team.users.length === 0;
    };
    $scope.hasTeams = function() {
      return $scope.teams.length > 0;
    };
    $scope.hasApps = function() {
      return $scope.appInstances.length > 0;
    };
    $scope.matrix = matrix = {};
    matrix.isLoading = false;
    matrix.hasAccess = function(team, appInstance) {
      if (angular.isString(appInstance) && appInstance === 'all') {
        return _.reduce($scope.appInstances, function(memo, elem) {
          return memo && (_.find(team.app_instances, function(i) {
            return i.id === elem.id;
          }) != null);
        }, true);
      } else {
        return _.find(team.app_instances, function(i) {
          return i.id === appInstance.id;
        }) != null;
      }
    };
    matrix.toggleAccess = function(team, appInstance) {
      var self;
      self = matrix;
      if (self.hasAccess(team, appInstance)) {
        return self.removeAccess(team, appInstance);
      } else {
        return self.addAccess(team, appInstance);
      }
    };
    matrix.addAccess = function(team, appInstance) {
      if (angular.isString(appInstance) && appInstance === 'all') {
        team.app_instances.length = 0;
        return angular.copy($scope.appInstances, team.app_instances);
      } else {
        if (_.find(team.app_instances, function(e) {
          return e.id === appInstance.id;
        }) == null) {
          return team.app_instances.push(appInstance);
        }
      }
    };
    matrix.removeAccess = function(team, appInstance) {
      var elem, idx;
      if (angular.isString(appInstance) && appInstance === 'all') {
        return team.app_instances.length = 0;
      } else {
        if ((elem = _.find(team.app_instances, function(e) {
          return e.id === appInstance.id;
        })) != null) {
          idx = team.app_instances.indexOf(elem);
          return team.app_instances.splice(idx, 1);
        }
      }
    };
    matrix.addTeam = function() {
      return addTeamModal.open();
    };
    matrix.removeTeam = function(team) {
      return teamDeletionModal.open(team);
    };
    matrix.compileHash = function(teams) {
      return _.reduce(teams, function(hash, t) {
        hash += t.id + ":";
        hash += _.sortBy(_.pluck(t.app_instances, 'id'), function(n) {
          return n;
        }).join();
        return hash += "::";
      }, "");
    };
    matrix.isChanged = function() {
      var self;
      self = matrix;
      return self.compileHash($scope.teams) !== self.compileHash($scope.originalTeams);
    };
    matrix.cancel = function() {
      return _.each($scope.teams, function(t) {
        var ot;
        ot = _.find($scope.originalTeams, function(e) {
          return e.id === t.id;
        });
        return angular.copy(ot.app_instances, t.app_instances);
      });
    };
    matrix.save = function() {
      var qs, self;
      self = matrix;
      self.isLoading = true;
      qs = [];
      _.each($scope.teams, function(team) {
        var realAppInstances;
        realAppInstances = team.app_instances.length > 0 ? team.app_instances : [{}];
        return qs.push(MnoeTeams.updateTeamAppInstances(team, realAppInstances));
      });
      return $q.all(qs).then((function() {}), self.errors = '', self.updateOriginalTeams(), function(errorsArray) {
        return self.errors = Utilities.processRailsError(errorsArray[0]);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    matrix.updateOriginalTeams = function() {
      return _.each($scope.teams, function(t) {
        var ot;
        ot = _.find($scope.originalTeams, function(e) {
          return e.id === t.id;
        });
        return angular.copy(t.app_instances, ot.app_instances);
      });
    };
    matrix.updateTeamName = function(team) {
      var origTeam;
      origTeam = _.find($scope.teams, function(t) {
        return t.id === team.id;
      });
      if (team.name.length === 0) {
        return team.name = origTeam.name;
      } else {
        return MnoeTeams.updateTeamName(team).then((function() {}), origTeam.name = team.name, function() {
          return team.name = origTeam.name;
        });
      }
    };
    $scope.addTeamModal = addTeamModal = {};
    addTeamModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/teams/modals/team-add-modal.html',
        size: 'lg',
        windowClass: 'inverse team-add-modal',
        scope: $scope
      }
    };
    addTeamModal.open = function() {
      var self;
      self = addTeamModal;
      self.model = {};
      self.$instance = $uibModal.open(self.config.instance);
      return self.isLoading = false;
    };
    addTeamModal.close = function() {
      var self;
      self = addTeamModal;
      return self.$instance.close();
    };
    addTeamModal.isProceedDisabled = function() {
      var self;
      self = addTeamModal;
      return (self.model.name == null) || self.model.name.length === 0;
    };
    addTeamModal.proceed = function() {
      var self;
      self = addTeamModal;
      self.isLoading = true;
      return MnoeTeams.addTeam(self.model).then(function(team) {
        self.errors = '';
        self.addToScope(team);
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    addTeamModal.addToScope = function(team) {
      $scope.teams.push(angular.copy(team));
      return $scope.originalTeams.push(angular.copy(team));
    };
    $scope.teamDeletionModal = teamDeletionModal = {};
    teamDeletionModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/teams/modals/team-delete-modal.html',
        size: 'lg',
        windowClass: 'inverse team-delete-modal',
        scope: $scope
      }
    };
    teamDeletionModal.open = function(team) {
      var self;
      self = teamDeletionModal;
      self.team = team;
      self.$instance = $uibModal.open(self.config.instance);
      self.isLoading = false;
      return self.errors = '';
    };
    teamDeletionModal.close = function() {
      var self;
      self = teamDeletionModal;
      return self.$instance.close();
    };
    teamDeletionModal.proceed = function() {
      var self;
      self = teamDeletionModal;
      self.isLoading = true;
      return MnoeTeams.deleteTeam(self.team.id).then(function(data) {
        self.errors = '';
        self.removeFromScope(self.team);
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    teamDeletionModal.removeFromScope = function(team) {
      var idx;
      team = _.find($scope.teams, function(t) {
        return t.id === team.id;
      });
      idx = $scope.teams.indexOf(team);
      if (idx >= 0) {
        $scope.teams.splice(idx, 1);
      }
      team = _.find($scope.originalTeams, function(t) {
        return t.id === team.id;
      });
      idx = $scope.originalTeams.indexOf(team);
      if (idx >= 0) {
        return $scope.originalTeams.splice(idx, 1);
      }
    };
    return $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue) {
      if (newValue != null) {
        return $q.all([MnoeTeams.getTeams(), MnoeAppInstances.getAppInstances()]).then(function(responses) {
          return $scope.initialize(responses[0], responses[1]);
        });
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationTeams', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/teams/organization-teams.html',
      controller: DashboardOrganizationTeamsCtrl
    };
  });

}).call(this);
