(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeOrganizations', ["$location", "$state", "$cookies", "$log", "$q", "MnoeApiSvc", "MnoeCurrentUser", function($location, $state, $cookies, $log, $q, MnoeApiSvc, MnoeCurrentUser) {
    var _self, organizationPromise, organizationsApi;
    _self = this;
    organizationsApi = MnoeApiSvc.all('organizations');
    this.selectedId = null;
    this.getSelectedId = function() {
      return _self.selectedId;
    };
    this.selected = null;
    this.getSelected = function() {
      return _self.selected;
    };
    this.list = function() {
      return organizationsApi.getList().then(function(response) {
        _self.list = response;
        return response;
      });
    };
    organizationPromise = null;
    this.get = function(id) {
      if (id == null) {
        id = null;
      }
      if (((id == null) || id === _self.selectedId) && (organizationPromise != null)) {
        return organizationPromise;
      }
      if (id != null) {
        _self.selectedId = id;
      }
      return MnoeApiSvc.one('/organizations', id).get().then(function(responseOrga) {
        _self.selected = responseOrga.plain();
        MnoeCurrentUser.get().then(function(response) {
          $cookies.put(response.id + "_dhb_ref_id", responseOrga.organization.id);
          return response;
        });
        return organizationPromise = $q.resolve(responseOrga.plain());
      });
    };
    this.create = function(organization) {
      return MnoeApiSvc.all('/organizations').post(organization).then(function(response) {
        var deferred;
        deferred = $q.defer();
        MnoeCurrentUser.refresh().then(function() {
          return deferred.resolve(response.organization);
        });
        return deferred.promise;
      });
    };
    this.update = function(organization) {
      var payload;
      payload = {
        organization: organization
      };
      return MnoeApiSvc.one('/organizations', _self.selectedId).customPUT(payload).then(function(response) {
        response = response.plain();
        angular.extend(_self.selected.organization, response.organization);
        angular.extend(_.find(MnoeCurrentUser.user.organizations, {
          id: response.organization.id
        }), response.organization);
        return response;
      });
    };
    this.updateCreditCard = function(creditCard) {
      return MnoeApiSvc.one('organizations', _self.selectedId).doPUT({
        "credit_card": creditCard
      }, '/update_billing').then(function(response) {
        return response.plain();
      });
    };
    this.purchaseApp = function(app, orgId) {
      if (orgId == null) {
        orgId = _self.selectedId;
      }
      return MnoeApiSvc.one('organizations', orgId).all('/app_instances').post({
        nid: app.nid
      }).then(function(response) {
        _self.get(orgId);
        return response;
      });
    };
    this.inviteMembers = function(invites) {
      var baseList, data, finalList;
      baseList = [];
      if (angular.isString(invites)) {
        baseList = invites.split("\n");
      } else {
        baseList = invites;
      }
      finalList = [];
      _.each(baseList, function(e) {
        if (angular.isObject(e)) {
          return finalList.push(e);
        } else {
          return finalList.push({
            email: e
          });
        }
      });
      data = {
        invites: finalList
      };
      return MnoeApiSvc.one('organizations', _self.selectedId).doPUT(data, '/invite_members').then(function(response) {
        return response.members;
      });
    };
    this.updateMember = function(member) {
      var data;
      data = {
        member: member
      };
      return MnoeApiSvc.one('organizations', _self.selectedId).doPUT(data, '/update_member').then(function(response) {
        if (member.email === _self.selected.current_user.email) {
          _self.selected.current_user.role = member.role;
        }
        return response.members;
      });
    };
    this.deleteMember = function(member) {
      var data;
      data = {
        member: _.pick(member, "email")
      };
      return MnoeApiSvc.one('organizations', _self.selectedId).doPUT(data, '/remove_member').then(function(response) {
        return response.members;
      });
    };
    this.getCurrentOrganisation = function() {
      var defer, dhbRefId;
      if (organizationPromise != null) {
        return organizationPromise;
      }
      defer = $q.defer();
      dhbRefId = $location.search().dhbRefId;
      $location.search('dhbRefId', null);
      if (dhbRefId) {
        $log.debug("MnoeOrganizations.getCurrentOrganisation: dhbRefId", dhbRefId);
        organizationPromise = _self.get(dhbRefId).then(function(response) {
          return defer.resolve(response);
        });
      } else {
        MnoeCurrentUser.get().then(function(response) {
          var val;
          if (response.organizations.length === 0) {
            defer.reject();
          }
          val = $cookies.get(response.id + "_dhb_ref_id");
          if (val != null) {
            $log.debug("MnoeOrganizations.getCurrentOrganisation: cookie", val);
            return organizationPromise = _self.get(val).then(function(response) {
              return defer.resolve(response);
            });
          } else {
            $log.debug("MnoeOrganizations.getCurrentOrganisation: first", response.organizations[0].id);
            return organizationPromise = _self.get(response.organizations[0].id).then(function(response) {
              return defer.resolve(response);
            });
          }
        });
      }
      return defer.promise;
    };
    this.role = {};
    _self.role.isSuperAdmin = function(role) {
      if (role) {
        return role === 'Super Admin';
      }
      return (_self.selected != null) && (_self.selected.current_user != null) && _self.selected.current_user.role === 'Super Admin';
    };
    _self.role.isAdmin = function(role) {
      if (role) {
        return role === 'Admin';
      }
      return (_self.selected != null) && (_self.selected.current_user != null) && _self.selected.current_user.role === 'Admin';
    };
    _self.role.isPowerUser = function(role) {
      if (role) {
        return role === 'Power User';
      }
      return (_self.selected != null) && (_self.selected.current_user != null) && _self.selected.current_user.role === 'Power User';
    };
    _self.role.isMember = function(role) {
      if (role) {
        return role === 'Member';
      }
      return (_self.selected != null) && (_self.selected.current_user != null) && _self.selected.current_user.role === 'Member';
    };
    _self.role.atLeastMember = function() {
      return true;
    };
    _self.role.atLeastPowerUser = function(role) {
      return _self.role.isPowerUser(role) || _self.role.isAdmin(role) || _self.role.isSuperAdmin(role);
    };
    _self.role.atLeastAdmin = function(role) {
      return _self.role.isAdmin(role) || _self.role.isSuperAdmin(role);
    };
    _self.role.atLeastSuperAdmin = function(role) {
      return _self.role.isSuperAdmin(role);
    };
    this.can = {};
    _self.can.read = {
      appInstance: function() {
        return _self.role.atLeastMember();
      },
      billing: function() {
        return _self.role.atLeastSuperAdmin();
      },
      member: function() {
        return _self.role.atLeastMember();
      },
      organizationSettings: function() {
        return _self.role.atLeastSuperAdmin();
      }
    };
    _self.can.create = {
      appInstance: function() {
        return _self.role.atLeastAdmin();
      },
      billing: function() {
        return _self.role.atLeastSuperAdmin();
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.role.atLeastAdmin() && (obj === null || obj.role !== 'Super Admin' || _self.role.isSuperAdmin());
      },
      organizationSettings: function() {
        return _self.role.atLeastSuperAdmin();
      }
    };
    _self.can.update = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.appInstance(obj);
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.billing(obj);
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.member(obj) && (obj.role !== 'Super Admin' || _self.role.isSuperAdmin());
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.organizationSettings(obj);
      }
    };
    _self.can.destroy = {
      appInstance: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.appInstance(obj);
      },
      billing: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.billing(obj);
      },
      member: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.member(obj) && (obj.role !== 'Super Admin' || _self.role.isSuperAdmin());
      },
      organizationSettings: function(obj) {
        if (obj == null) {
          obj = null;
        }
        return _self.can.create.organizationSettings(obj);
      }
    };
    _self.can.connect = {
      appInstance: function() {
        return _self.role.atLeastAdmin();
      }
    };
    return this;
  }]);

}).call(this);
