(function() {
  angular.module('mnoEnterpriseAngular').controller('OnboardingStep3Controller', ["MnoeOrganizations", "MnoeAppInstances", function(MnoeOrganizations, MnoeAppInstances) {
    'ngInject';
    var vm;
    vm = this;
    vm.isFetchingAppInstances = true;
    MnoeAppInstances.getAppInstances().then(function(response) {
      vm.appInstances = angular.copy(response);
      vm.nbApps = _.size(vm.appInstances);
      return vm.nbAppsToConnect = _.size(_.filter(vm.appInstances, function(ai) {
        return MnoeAppInstances.installStatus(ai) === "INSTALLED_CONNECT";
      }));
    })["finally"](function() {
      return vm.isFetchingAppInstances = false;
    });
  }]);

}).call(this);
