(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeAppInstances', ["$q", "MnoeApiSvc", "MnoeOrganizations", "MnoLocalStorage", "MnoeCurrentUser", "LOCALSTORAGE", function($q, MnoeApiSvc, MnoeOrganizations, MnoLocalStorage, MnoeCurrentUser, LOCALSTORAGE) {
    var _self, appInstancesPromise, fetchAppInstances, processAppInstances;
    _self = this;
    this.appInstances = [];
    appInstancesPromise = null;
    this.getAppInstances = function() {
      var cache, deferred;
      if (appInstancesPromise != null) {
        return appInstancesPromise;
      }
      deferred = $q.defer();
      cache = MnoLocalStorage.getObject(MnoeCurrentUser.user.id + "_" + LOCALSTORAGE.appInstancesKey);
      if (cache != null) {
        _self.appInstances = cache;
        deferred.resolve(cache);
      } else {
        fetchAppInstances().then(function(response) {
          return deferred.resolve(response);
        });
      }
      return appInstancesPromise = deferred.promise;
    };
    this.refreshAppInstances = function() {
      appInstancesPromise = null;
      _self.clearCache();
      _self.emptyAppInstances();
      return fetchAppInstances();
    };
    fetchAppInstances = function() {
      var defer;
      defer = $q.defer();
      MnoeOrganizations.get().then(function() {
        return _self.appInstancesPromise = MnoeApiSvc.one('organizations', MnoeOrganizations.selectedId).one('/app_instances').get().then(function(response) {
          var appInstances;
          response = response.plain();
          appInstances = processAppInstances(response);
          MnoLocalStorage.setObject(MnoeCurrentUser.user.id + "_" + LOCALSTORAGE.appInstancesKey, appInstances);
          return defer.resolve(appInstances);
        });
      });
      return defer.promise;
    };
    processAppInstances = function(response) {
      var arr;
      _self.appInstances.length = 0;
      arr = _.values(response.app_instances);
      Array.prototype.push.apply(_self.appInstances, arr);
      return _self.appInstances;
    };
    this.oAuthConnectPath = function(instance, extra_params) {
      var redirect;
      if (extra_params == null) {
        extra_params = '';
      }
      _self.clearCache();
      _self.emptyAppInstances();
      redirect = window.encodeURIComponent("" + location.pathname + location.hash);
      return "/mnoe/webhook/oauth/" + instance.uid + "/authorize?redirect_path=" + redirect + "&" + extra_params;
    };
    this.terminate = function(id) {
      return MnoeApiSvc.one('app_instances', id).remove().then(function() {
        _.remove(_self.appInstances, {
          id: id
        });
        return MnoLocalStorage.setObject(MnoeCurrentUser.user.id + "_" + LOCALSTORAGE.appInstancesKey, _self.appInstances);
      });
    };
    this.emptyAppInstances = function() {
      appInstancesPromise = null;
      return this.appInstances.length = 0;
    };
    this.clearCache = function() {
      return MnoLocalStorage.removeItem(MnoeCurrentUser.user.id + "_" + LOCALSTORAGE.appInstancesKey);
    };
    this.installStatus = function(appInstance) {
      if (appInstance.app_nid !== 'office-365' && appInstance.stack === 'connector' && !appInstance.oauth_keys_valid) {
        return "INSTALLED_CONNECT";
      } else {
        return "INSTALLED_LAUNCH";
      }
    };
    return this;
  }]);

}).call(this);
