(function() {
  var DashboardOrganizationTeamListCtrl;

  DashboardOrganizationTeamListCtrl = ["$scope", "$window", "$uibModal", "$q", "MnoeCurrentUser", "MnoeOrganizations", "MnoeTeams", "MnoeAppInstances", "Utilities", function($scope, $window, $uibModal, $q, MnoeCurrentUser, MnoeOrganizations, MnoeTeams, MnoeAppInstances, Utilities) {
    'ngInject';
    var memberAddModal, memberRemovalModal;
    $scope.isLoading = true;
    $scope.teams = [];
    $scope.initialize = function(teams) {
      var realTeams;
      realTeams = [];
      if ($scope.canManageTeam()) {
        realTeams = teams;
      } else {
        _.each(teams, function(t) {
          if ($scope.teamHasUser(t, MnoeCurrentUser.user)) {
            return realTeams.push(t);
          }
        });
      }
      angular.copy(realTeams, $scope.teams);
      return $scope.isLoading = false;
    };
    $scope.isTeamEmpty = function(team) {
      return team.users.length === 0;
    };
    $scope.hasTeams = function() {
      return $scope.teams.length > 0;
    };
    $scope.canManageTeam = function() {
      return MnoeOrganizations.can.create.member();
    };
    $scope.teamHasUser = function(team, user) {
      return _.find(team.users, function(u) {
        return u.id === user.id;
      }) != null;
    };
    $scope.memberAddModal = memberAddModal = {};
    memberAddModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/team-list/modals/member-add-modal.html',
        size: 'lg',
        windowClass: 'inverse team-member-add-modal',
        scope: $scope
      }
    };
    memberAddModal.open = function(team) {
      var self;
      self = memberAddModal;
      self.team = team;
      self.users = [];
      self.userList = self.getAvailableUsers(team);
      self.$instance = $uibModal.open(self.config.instance);
      return self.isLoading = false;
    };
    memberAddModal.close = function() {
      var self;
      self = memberAddModal;
      return self.$instance.close();
    };
    memberAddModal.getAvailableUsers = function(team) {
      var list, self;
      self = memberAddModal;
      list = [];
      _.each(MnoeOrganizations.selected.organization.members, function(m) {
        if (_.find(team.users, function(u) {
          return u.id === m.id;
        }) == null) {
          if (m.entity === 'User') {
            return list.push(m);
          }
        }
      });
      return list;
    };
    memberAddModal.canAddUsers = function() {
      var self;
      self = memberAddModal;
      return self.userList.length > 0;
    };
    memberAddModal.hasUser = function(user) {
      var self;
      self = memberAddModal;
      return _.contains(self.users, user);
    };
    memberAddModal.toggleUser = function(user) {
      var self;
      self = memberAddModal;
      if (self.hasUser(user)) {
        return self.removeUser(user);
      } else {
        return self.addUser(user);
      }
    };
    memberAddModal.addUser = function(user) {
      var self;
      self = memberAddModal;
      if (!self.hasUser(user)) {
        return self.users.push(user);
      }
    };
    memberAddModal.removeUser = function(user) {
      var idx, self;
      self = memberAddModal;
      if ((idx = self.users.indexOf(user)) >= 0) {
        return self.users.splice(idx, 1);
      }
    };
    memberAddModal.proceed = function() {
      var self;
      self = memberAddModal;
      self.isLoading = true;
      return MnoeTeams.addUsers(self.team.id, self.users).then(function(users) {
        self.errors = '';
        angular.copy(users, self.team.users);
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    $scope.memberRemovalModal = memberRemovalModal = {};
    memberRemovalModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/team-list/modals/member-removal-modal.html',
        size: 'lg',
        windowClass: 'inverse team-member-removal-modal',
        scope: $scope
      }
    };
    memberRemovalModal.open = function(team, user) {
      var self;
      self = memberRemovalModal;
      self.team = team;
      self.user = user;
      self.$instance = $uibModal.open(self.config.instance);
      return self.isLoading = false;
    };
    memberRemovalModal.close = function() {
      var self;
      self = memberRemovalModal;
      return self.$instance.close();
    };
    memberRemovalModal.proceed = function() {
      var self;
      self = memberRemovalModal;
      self.isLoading = true;
      return MnoeTeams.removeUser(self.team.id, self.user).then(function(users) {
        self.errors = '';
        angular.copy(users, self.team.users);
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue) {
      if (newValue != null) {
        return MnoeTeams.getTeams().then(function(responses) {
          return $scope.initialize(responses);
        });
      }
    });
    return $scope.$watch(function() {
      return MnoeTeams.teams.length;
    }, function(newValue) {
      if (newValue != null) {
        return $scope.initialize(MnoeTeams.teams);
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationTeamList', function() {
    return {
      restrict: 'A',
      scope: {
        title: '@'
      },
      templateUrl: 'app/views/company/team-list/team-list.html',
      controller: DashboardOrganizationTeamListCtrl
    };
  });

}).call(this);
