(function() {
  angular.module('mnoEnterpriseAngular').controller('MarketplaceChooseOrgaModalCtrl', ["$scope", "$uibModalInstance", "MnoeOrganizations", "MnoeCurrentUser", "app", function($scope, $uibModalInstance, MnoeOrganizations, MnoeCurrentUser, app) {
    var filterAuthorizedOrga;
    $scope.organizations = {};
    $scope.current_organization = {};
    $scope.authorized_organizations = {};
    $scope.hasAuthorizedOrganizations = false;
    $scope.isLoading = true;
    $scope.app = app;
    $scope.current_organization = {
      id: MnoeOrganizations.selectedId
    };
    $scope.isUserAuthorized = function(orgId) {
      var currentUserRole;
      currentUserRole = $scope.organizations[orgId].current_user_role;
      return MnoeOrganizations.role.atLeastAdmin(currentUserRole);
    };
    $scope.updateUserAuthorization = function() {
      return $scope.current_organization.isUserAuthorized = $scope.isUserAuthorized($scope.current_organization.id);
    };
    $scope.addApplication = function() {
      $scope.isLoading = true;
      return MnoeOrganizations.purchaseApp($scope.app, $scope.current_organization.id).then(function(response) {
        return $uibModalInstance.close(response);
      })["finally"](function() {
        return $scope.isLoading = false;
      });
    };
    $scope.closeChooseOrgaModal = function() {
      return $uibModalInstance.dismiss();
    };
    filterAuthorizedOrga = function(organizations) {
      return _.forEach(organizations, function(org) {
        $scope.organizations[org.id] = org;
        if (MnoeOrganizations.role.atLeastAdmin(org.current_user_role)) {
          return $scope.authorized_organizations[org.id] = org;
        }
      });
    };
    MnoeCurrentUser.get().then(function(response) {
      filterAuthorizedOrga(response.organizations);
      $scope.hasAuthorizedOrganizations = !_.isEmpty($scope.authorized_organizations);
      return $scope.isLoading = false;
    });
  }]);

}).call(this);
