(function() {
  angular.module('mnoEnterpriseAngular').run(["editableOptions", function(editableOptions) {
    return editableOptions.theme = 'bs3';
  }]).run(["$rootScope", "APP_NAME", function($rootScope, APP_NAME) {
    return $rootScope.app_name = APP_NAME;
  }]).run(["$rootScope", function($rootScope) {
    return $rootScope.$on('$viewContentLoaded', function() {
      return window.scrollTo(0, 0);
    });
  }]).run(["$window", "$translate", "LOCALES", function($window, $translate, LOCALES) {
    var found, locale, path, re;
    path = $window.location.pathname;
    re = /^\/([A-Za-z]{2})\/dashboard\//i;
    found = path.match(re);
    if (found != null) {
      locale = found[1];
    } else {
      locale = LOCALES.preferredLocale;
    }
    return $translate.use(locale);
  }]).run(["$templateCache", function($templateCache) {
    return $templateCache.put('directives/toast/toast.html', '<div class="{{toastClass}} {{toastType}}" ng-click="tapToast()">\n  <div ng-switch on="allowHtml">\n    <div ng-switch-default ng-if="title" class="{{titleClass}}" aria-label="{{title | translate}}">{{title | translate:extraData}}</div>\n    <div ng-switch-default class="{{messageClass}}" aria-label="{{message | translate:extraData}}">{{message | translate:extraData}}</div>\n    <div ng-switch-when="true" ng-if="title" class="{{titleClass}}" ng-bind-html="title | translate:extraData"></div>\n    <div ng-switch-when="true" class="{{messageClass}}" ng-bind-html="message | translate:extraData"></div>\n  </div>\n  <progress-bar ng-if="progressBar"></progress-bar>\n</div>');
  }]).run(["toastr", "$location", function(toastr, $location) {
    var flash, message;
    if (flash = $location.search().flash) {
      message = JSON.parse(flash);
      toastr[message.type](message.msg, _.capitalize(message.type), {
        timeout: 10000
      });
      return $location.search('flash', null);
    }
  }]).run(["$rootScope", "$timeout", "AnalyticsSvc", function($rootScope, $timeout, AnalyticsSvc) {
    $timeout((function() {
      return AnalyticsSvc.init();
    }));
    return $rootScope.$on('$stateChangeSuccess', function() {
      return AnalyticsSvc.update();
    });
  }]).run(["$rootScope", "$q", "$location", "$stateParams", "MnoeCurrentUser", "MnoeOrganizations", "MnoeMarketplace", "MnoeAppInstances", function($rootScope, $q, $location, $stateParams, MnoeCurrentUser, MnoeOrganizations, MnoeMarketplace, MnoeAppInstances) {
    var _self, orgPromise, userPromise;
    _self = this;
    $rootScope.isLoggedIn = false;
    userPromise = MnoeCurrentUser.get();
    _self.appInstancesDeferred = $q.defer();
    orgPromise = MnoeOrganizations.getCurrentOrganisation().then(function(response) {
      MnoeAppInstances.getAppInstances().then(function(appInstances) {
        return _self.appInstancesDeferred.resolve(appInstances);
      });
      return response;
    });
    return $q.all([userPromise, orgPromise, _self.appInstancesDeferred.promise]).then(function() {
      $rootScope.isLoggedIn = true;
      return MnoeMarketplace.getApps();
    })["catch"](function() {
      $rootScope.isLoggedIn = true;
      return $rootScope.hasNoOrganisations = true;
    });
  }]);

}).call(this);
