(function() {
  var DashboardOrganizationBillingCtrl;

  DashboardOrganizationBillingCtrl = ["$scope", "$window", "MnoeOrganizations", "MnoeConfig", function($scope, $window, MnoeOrganizations, MnoeConfig) {
    'ngInject';
    $scope.isCreditShown = function() {
      var b;
      b = $scope.billing;
      return b && b.credit && b.credit.value > 0;
    };
    $scope.payment_enabled = MnoeConfig.isPaymentEnabled();
    return $scope.$watch(MnoeOrganizations.getSelected, function(val) {
      if (val != null) {
        return $scope.billing = MnoeOrganizations.selected.billing;
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationBilling', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/billing/organization-billing.html',
      controller: DashboardOrganizationBillingCtrl
    };
  });

}).call(this);
