(function() {
  angular.module('mnoEnterpriseAngular').component('mnoWithoutOrganisation', {
    templateUrl: 'app/components/mno-without-organisation/mno-without-organisation.html',
    controller: ["$rootScope", "$state", "$q", "$uibModal", "MnoeCurrentUser", "MnoeOrganizations", "MnoeConfig", function($rootScope, $state, $q, $uibModal, MnoeCurrentUser, MnoeOrganizations, MnoeConfig) {
      var ctrl;
      ctrl = this;
      ctrl.canCreateOrganisation = MnoeConfig.isOrganizationManagementEnabled();
      ctrl.openCreateOrgaModal = function() {
        var modalInstance;
        modalInstance = $uibModal.open({
          templateUrl: 'app/components/dashboard-company-selectbox/modals/create-company.html',
          controller: 'CreateCompanyModalCtrl',
          size: 'lg',
          windowClass: 'inverse',
          backdrop: 'static'
        });
        return modalInstance.result.then(function(organization) {
          return MnoeOrganizations.get(organization.id).then(function() {
            return $rootScope.hasNoOrganisations = false;
          });
        });
      };
      MnoeCurrentUser.get().then(function(response) {
        return ctrl.user = response;
      });
    }]
  });

}).call(this);
