(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeMarketplace', ["$log", "MnoeApiSvc", "MnoeFullApiSvc", function($log, MnoeApiSvc, MnoeFullApiSvc) {
    var _self, marketplaceApi, marketplacePromise, marketplaceResponse;
    _self = this;
    marketplaceApi = MnoeApiSvc.oneUrl('/marketplace');
    marketplacePromise = null;
    marketplaceResponse = null;
    this.getApps = function() {
      if (marketplacePromise != null) {
        return marketplacePromise;
      }
      return marketplacePromise = marketplaceApi.get().then(function(response) {
        marketplaceResponse = response;
        return response;
      });
    };
    this.findApp = function(nid) {
      return _.find(marketplaceResponse.apps, function(a) {
        return a.nid === nid;
      });
    };
    this.getReview = function(appId, reviewId) {
      return MnoeApiSvc.one('marketplace', parseInt(appId)).one('app_reviews', parseInt(reviewId)).get();
    };
    this.getReviews = function(appId, limit, offset, sort) {
      var params;
      params = {
        order_by: sort,
        limit: limit,
        offset: offset
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).all('app_feedbacks').getList(params);
    };
    this.getQuestions = function(appId, limit, offset, search) {
      var params;
      params = {
        limit: limit,
        offset: offset,
        search: search
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).all('app_questions').getList(params);
    };
    this.addAppReview = function(appId, data) {
      var payload;
      payload = {
        app_review: data
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).post('/app_reviews', payload).then(function(response) {
        var app_review;
        app_review = response.data.plain();
        return app_review;
      });
    };
    this.editReview = function(appId, feedback_id, feedback) {
      var payload;
      payload = feedback;
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_feedbacks/" + feedback_id).patch(payload).then(function(response) {
        var app_review;
        app_review = response.data.plain();
        return app_review;
      });
    };
    this.deleteReview = function(appId, feedback_id) {
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_feedbacks/" + feedback_id).remove().then(function(response) {
        var app_review;
        app_review = response.data.plain();
        return app_review;
      });
    };
    this.addAppReviewComment = function(appId, data) {
      var payload;
      payload = {
        app_comment: data
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).post('/app_comments', payload).then(function(response) {
        var app_comment;
        app_comment = response.data.plain();
        return app_comment;
      });
    };
    this.editComment = function(appId, comment_id, comment) {
      var payload;
      payload = {
        app_comment: comment
      };
      return MnoeFullApiSvc.one("marketplace/" + appId + "/app_comments/" + comment_id).patch(payload).then(function(response) {
        var app_comment;
        app_comment = response.data.plain();
        return app_comment;
      });
    };
    this.deleteComment = function(appId, comment_id) {
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_comments/" + comment_id).remove().then(function(response) {
        var app_comment;
        app_comment = response.data.plain();
        return app_comment;
      });
    };
    this.addAppQuestion = function(appId, data) {
      var payload;
      payload = {
        app_question: data
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).post('/app_questions', payload).then(function(response) {
        var app_question;
        app_question = response.data.plain();
        return app_question;
      });
    };
    this.editQuestion = function(appId, question_id, question) {
      var payload;
      payload = question;
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_questions/" + question_id).patch(payload).then(function(response) {
        var app_question;
        app_question = response.data.plain();
        return app_question;
      });
    };
    this.deleteQuestion = function(appId, question_id) {
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_questions/" + question_id).remove().then(function(response) {
        var app_question;
        app_question = response.data.plain();
        return app_question;
      });
    };
    this.addAppQuestionAnswer = function(appId, data) {
      var payload;
      payload = {
        app_answer: data
      };
      return MnoeFullApiSvc.one('marketplace', parseInt(appId)).post('/app_answers', payload).then(function(response) {
        var app_answer;
        app_answer = response.data.plain();
        return app_answer;
      });
    };
    this.editAnswer = function(appId, answer_id, answer) {
      var payload;
      payload = answer;
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_answers/" + answer_id).patch(payload).then(function(response) {
        var app_answer;
        app_answer = response.data.plain();
        return app_answer;
      });
    };
    this.deleteAnswer = function(appId, answer_id) {
      return MnoeFullApiSvc.one("marketplace/" + (parseInt(appId)) + "/app_answers/" + answer_id).remove().then(function(response) {
        var app_answer;
        app_answer = response.data.plain();
        return app_answer;
      });
    };
    return this;
  }]);

}).call(this);
