(function() {
  var DashboardOrganizationMembersCtrl;

  DashboardOrganizationMembersCtrl = ["$scope", "$uibModal", "$sce", "MnoeOrganizations", "MnoeCurrentUser", "MnoeTeams", "Utilities", function($scope, $uibModal, $sce, MnoeOrganizations, MnoeCurrentUser, MnoeTeams, Utilities) {
    'ngInject';
    var deletionModal, editionModal, inviteModal, rolesToDisplay, updateNbOfSuperAdmin;
    $scope.members = [];
    $scope.teams = [];
    $scope.isLoading = true;
    $scope.initialize = function(members, teams) {
      if (teams == null) {
        teams = nil;
      }
      $scope.members = members;
      if (teams) {
        $scope.teams = teams;
      }
      $scope.isLoading = false;
      return updateNbOfSuperAdmin();
    };
    $scope.editMember = function(member) {
      return $scope.editionModal.open(member);
    };
    $scope.removeMember = function(member) {
      return $scope.deletionModal.open(member);
    };
    $scope.inviteMembers = function() {
      return $scope.inviteModal.open();
    };
    $scope.isInviteShown = function() {
      return MnoeOrganizations.can.create.member();
    };
    $scope.isEditShown = function(member) {
      if (!$scope.hasManySuperAdmin && member.email === MnoeCurrentUser.user.email && $scope.user_role === 'Super Admin') {
        return false;
      } else {
        return MnoeOrganizations.can.update.member(member);
      }
    };
    $scope.isRemoveShown = function(member) {
      return MnoeOrganizations.can.destroy.member(member, $scope.hasManySuperAdmin) && member.email !== MnoeCurrentUser.user.email;
    };
    $scope.memberRoleLabel = function(member) {
      if (member.entity === 'User') {
        return member.role;
      } else {
        return "Invited (" + member.role + ")";
      }
    };
    updateNbOfSuperAdmin = function() {
      return $scope.hasManySuperAdmin = _.filter($scope.members, {
        'role': 'Super Admin'
      }).length > 1;
    };
    rolesToDisplay = function() {
      if (!$scope.user_role) {
        $scope.user_role = _.find(MnoeCurrentUser.user.organizations, {
          id: MnoeOrganizations.selectedId
        }).current_user_role;
      }
      if ($scope.user_role === 'Super Admin') {
        return editionModal.config.roles = ['Member', 'Power User', 'Admin', 'Super Admin'];
      } else {
        return editionModal.config.roles = ['Member', 'Power User', 'Admin'];
      }
    };
    $scope.editionModal = editionModal = {};
    editionModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/members/modals/edition-modal.html',
        size: 'lg',
        windowClass: 'inverse member-edit',
        scope: $scope
      }
    };
    rolesToDisplay();
    editionModal.open = function(member) {
      var self;
      self = editionModal;
      self.member = member;
      self.selectedRole = member.role;
      self.$instance = $uibModal.open(self.config.instance);
      self.isLoading = false;
      return editionModal.member = member;
    };
    editionModal.close = function() {
      var self;
      self = editionModal;
      return self.$instance.close();
    };
    editionModal.select = function(role) {
      return editionModal.selectedRole = role;
    };
    editionModal.classForRole = function(role) {
      var self;
      self = editionModal;
      if (role === self.member.role) {
        return 'btn-info';
      } else if (role === self.selectedRole) {
        return 'btn-warning';
      } else {
        return '';
      }
    };
    editionModal.isChangeDisabled = function() {
      return editionModal.member.role === editionModal.selectedRole;
    };
    editionModal.change = function() {
      var obj, self;
      self = editionModal;
      self.isLoading = true;
      obj = {
        email: self.member.email,
        role: self.selectedRole
      };
      return MnoeOrganizations.updateMember(obj).then(function(members) {
        self.errors = '';
        angular.copy(members, $scope.members);
        updateNbOfSuperAdmin();
        if (obj.email === MnoeCurrentUser.user.email) {
          $scope.user_role = obj.role;
          rolesToDisplay();
        }
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    $scope.deletionModal = deletionModal = {};
    deletionModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/members/modals/removal-modal.html',
        size: 'lg',
        windowClass: 'inverse member-edit',
        scope: $scope
      }
    };
    deletionModal.open = function(member) {
      var self;
      self = deletionModal;
      self.member = member;
      self.$instance = $uibModal.open(self.config.instance);
      self.isLoading = false;
      return self.member = member;
    };
    deletionModal.close = function() {
      var self;
      self = deletionModal;
      return self.$instance.close();
    };
    deletionModal.hasName = function() {
      var m;
      m = deletionModal.member;
      return m.entity === 'User' && (m.name != null);
    };
    deletionModal.remove = function() {
      var self;
      self = deletionModal;
      self.isLoading = true;
      return MnoeOrganizations.deleteMember(deletionModal.member).then(function(members) {
        self.errors = '';
        angular.copy(members, $scope.members);
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    $scope.inviteModal = inviteModal = {};
    inviteModal.config = {
      instance: {
        backdrop: 'static',
        templateUrl: 'app/views/company/members/modals/invite-modal.html',
        size: 'lg',
        windowClass: 'inverse member-edit',
        scope: $scope
      },
      defaultRole: 'Member',
      roles: function() {
        var list;
        list = ['Member', 'Power User', 'Admin'];
        if (MnoeOrganizations.role.isSuperAdmin()) {
          list.push('Super Admin');
        }
        return list;
      },
      teams: function() {
        return $scope.teams;
      }
    };
    inviteModal.open = function() {
      var self;
      self = inviteModal;
      self.$instance = $uibModal.open(self.config.instance);
      self.isLoading = false;
      self.members = [];
      self.userEmails = '';
      self.step = 'enterEmails';
      self.roleList = self.config.roles();
      self.teamList = self.config.teams();
      return self.invalidEmails = [];
    };
    inviteModal.close = function() {
      var self;
      self = inviteModal;
      return self.$instance.close();
    };
    inviteModal.isTeamListShown = function() {
      return inviteModal.teamList.length > 0;
    };
    inviteModal.title = function() {
      var self;
      self = inviteModal;
      if (self.step === 'enterEmails') {
        return $sce.trustAsHtml("Enter email addresses");
      } else {
        return $sce.trustAsHtml("Select role for each new member");
      }
    };
    inviteModal.labelForAction = function() {
      if (inviteModal.step === 'enterEmails') {
        return "Next";
      } else {
        return "Invite";
      }
    };
    inviteModal.next = function() {
      var self;
      self = inviteModal;
      if (self.step === 'enterEmails') {
        return inviteModal.processEmails();
      } else {
        return inviteModal.inviteMembers();
      }
    };
    inviteModal.isNextEnabled = function() {
      var self;
      self = inviteModal;
      return self.step === 'defineRoles' || (self.step === 'enterEmails' && self.userEmails.length > 0);
    };
    inviteModal.oneValidEmail = function() {
      var res, self;
      self = inviteModal;
      res = false;
      _.each(self.userEmails.split("\n"), function(email) {
        if (email.match(email_regexp)) {
          return res = true;
        }
      });
      return res;
    };
    inviteModal.processEmails = function() {
      var self;
      self = inviteModal;
      self.isLoading = true;
      self.members = [];
      self.invalidEmails = [];
      _.each(self.userEmails.split("\n"), function(email) {
        var email_regexp;
        email_regexp = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
        if (email.match(email_regexp)) {
          return self.members.push({
            email: email,
            role: self.config.defaultRole
          });
        } else {
          return self.invalidEmails.push(email);
        }
      });
      self.isLoading = false;
      if (self.invalidEmails.length === 0) {
        return self.step = 'defineRoles';
      }
    };
    inviteModal.inviteMembers = function() {
      var self;
      self = inviteModal;
      self.isLoading = true;
      return MnoeOrganizations.inviteMembers(self.members).then(function(members) {
        self.errors = '';
        angular.copy(members, $scope.members);
        updateNbOfSuperAdmin();
        return self.close();
      }, function(errors) {
        return self.errors = Utilities.processRailsError(errors);
      })["finally"](function() {
        return self.isLoading = false;
      });
    };
    return $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue) {
      if (newValue != null) {
        return MnoeTeams.getTeams().then(function() {
          return $scope.initialize(MnoeOrganizations.selected.organization.members, MnoeTeams.teams);
        });
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationMembers', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/members/organization-members.html',
      controller: DashboardOrganizationMembersCtrl
    };
  });

}).call(this);
