(function() {
  angular.module('mnoEnterpriseAngular').directive('mnoImpersonationBar', function() {
    return {
      restrict: 'EA',
      template: '<div id="impersonate-bar" class="row" ng-show="currentUser && currentUser.current_impersonator">\n  <div class="col-sm-6 col-sm-offset-3 col-md-8 col-md-offset-2 text-center">\n    You are logged in as {{currentUser.name}} {{currentUser.surname}} ({{currentUser.email}})\n    <a class="btn btn-sm btn-warning" type="submit" ng-click="exitImpersonation()">Revert to admin</a>\n  </div>\n</div>',
      controller: ["$scope", "$window", "MnoeCurrentUser", function($scope, $window, MnoeCurrentUser) {
        $scope.exitImpersonation = function() {
          return $window.location.href = "/mnoe/impersonate/revert";
        };
        return MnoeCurrentUser.get().then(function(response) {
          return $scope.currentUser = response;
        });
      }]
    };
  });

}).call(this);
