(function() {
  angular.module('mnoEnterpriseAngular').controller('OnboardingStep2Controller', ["$q", "$state", "$uibModal", "MnoeMarketplace", "MnoeOrganizations", "MnoeAppInstances", "DOC_LINKS", function($q, $state, $uibModal, MnoeMarketplace, MnoeOrganizations, MnoeAppInstances, DOC_LINKS) {
    'ngInject';
    var MAX_APPS_ONBOARDING, compareSharedEntities, vm;
    vm = this;
    MAX_APPS_ONBOARDING = 4;
    vm.searchTerm = '';
    vm.isLoading = true;
    vm.connecDocUri = DOC_LINKS.connecDoc;
    vm.selectedApps = [];
    vm.appsFilter = function(app) {
      if (vm.selectedCategory) {
        return _.contains(app.categories, vm.selectedCategory);
      } else {
        return true;
      }
    };
    vm.toggleApp = function(app) {
      if (vm.maxAppsSelected && !app.checked) {
        return;
      }
      app.checked = !app.checked;
      if (app.checked) {
        vm.selectedApps.push(app);
      } else {
        _.remove(vm.selectedApps, app);
      }
      vm.maxAppsSelected = vm.selectedApps.length === MAX_APPS_ONBOARDING;
      compareSharedEntities(vm.selectedApps);
    };
    compareSharedEntities = function(apps) {
      var appEntities, listEntities;
      appEntities = [];
      listEntities = [];
      _.each(apps, function(a) {
        var entities;
        entities = _.map(a.shared_entities, 'shared_entity_name');
        listEntities = _(listEntities).concat(entities).value();
        return appEntities.push({
          nid: a.nid,
          logo: a.logo,
          name: a.name,
          entities: entities
        });
      });
      listEntities = _.uniq(listEntities);
      vm.appEntities = appEntities;
      return vm.listEntities = listEntities;
    };
    vm.containsEntity = function(entities, entity) {
      return _.includes(entities, entity);
    };
    vm.connectApps = function() {
      var appsInstancesToDelete, appsToAdd, appsToDelete, promises;
      vm.isConnectingApps = true;
      appsToAdd = _.filter(vm.marketplace.apps, function(app) {
        return app.checked === true && !_.includes(vm.originalAppNids, app.nid);
      });
      appsToDelete = _.filter(vm.marketplace.apps, function(app) {
        return app.checked === false && _.includes(vm.originalAppNids, app.nid);
      });
      appsInstancesToDelete = _.filter(vm.appInstances, function(ai) {
        return _.includes(_.map(appsToDelete, 'nid'), ai.app_nid);
      });
      promises = _.map(appsToAdd, function(app) {
        return MnoeOrganizations.purchaseApp(app);
      });
      promises = _(promises).concat(_.map(appsInstancesToDelete, function(ai) {
        return MnoeAppInstances.terminate(ai.id);
      })).value();
      return $q.all(promises)["finally"](function() {
        return MnoeAppInstances.refreshAppInstances().then(function() {
          vm.isConnectingApps = false;
          return $state.go('onboarding.step3');
        });
      });
    };
    vm.openInfoModal = function(app) {
      return $uibModal.open({
        templateUrl: 'app/views/onboarding/modals/app-infos.html',
        controller: 'MnoAppInfosCtrl',
        controllerAs: 'vm',
        size: 'lg',
        resolve: {
          app: app
        }
      });
    };
    $q.all({
      appInstances: MnoeAppInstances.getAppInstances(),
      marketplace: MnoeMarketplace.getApps()
    }).then(function(response) {
      vm.appInstances = angular.copy(response.appInstances);
      vm.marketplace = angular.copy(response.marketplace.plain());
      vm.originalAppNids = _.map(vm.appInstances, 'app_nid');
      return _.each(_.filter(vm.marketplace.apps, function(app) {
        return _.includes(vm.originalAppNids, app.nid);
      }), function(a) {
        return vm.toggleApp(a);
      });
    })["finally"](function() {
      return vm.isLoading = false;
    });
  }]);

}).call(this);
