(function() {
  angular.module('mnoEnterpriseAngular').controller('DashboardMarketplaceAppCtrl', ["$q", "$scope", "$stateParams", "$state", "$sce", "$window", "$uibModal", "$anchorScroll", "$location", "toastr", "MnoeMarketplace", "MnoeOrganizations", "MnoeCurrentUser", "MnoeAppInstances", "MnoeProvisioning", "MnoConfirm", "MnoErrorsHandler", "MnoeConfig", function($q, $scope, $stateParams, $state, $sce, $window, $uibModal, $anchorScroll, $location, toastr, MnoeMarketplace, MnoeOrganizations, MnoeCurrentUser, MnoeAppInstances, MnoeProvisioning, MnoConfirm, MnoErrorsHandler, MnoeConfig) {
    var fetchQuestions, fetchReviews, updateAverageRating, vm;
    vm = this;
    vm.isLoading = true;
    vm.app = {};
    vm.searchWord = "";
    vm.appInstance = null;
    vm.conflictingApp = null;
    vm.isPriceShown = MnoeConfig.isMarketplacePricingEnabled();
    vm.isProvisioningEnabled = MnoeConfig.isProvisioningEnabled();
    vm.isReviewingEnabled = MnoeConfig.areMarketplaceReviewsEnabled();
    vm.areQuestionsEnabled = MnoeConfig.areMarketplaceQuestionsEnabled();
    vm.averageRating = 5;
    vm.sortReviewsBy = 'created_at.desc';
    vm.initialize = function(app, appInstance, product) {
      var cart, currency, plans;
      vm.userId = MnoeCurrentUser.user.id;
      vm.adminRole = MnoeCurrentUser.user.admin_role;
      angular.copy(app, vm.app);
      vm.appInstance = appInstance;
      vm.app.description = $sce.trustAsHtml(app.description);
      vm.isExternallyProvisioned = vm.isProvisioningEnabled && (product != null ? product.externally_provisioned : void 0);
      plans = vm.app.pricing_plans;
      currency = MnoeConfig.marketplaceCurrency();
      vm.pricing_plans = plans[currency] || plans.AUD || plans["default"];
      MnoeOrganizations.get().then(function(response) {
        return vm.user_role = response.current_user.role;
      });
      if (vm.isReviewingEnabled) {
        vm.reviews = {
          loading: true,
          nbItems: 5,
          page: 1,
          pageChangedCb: function(appId, nbItems, page) {
            var offset;
            vm.reviews.nbItems = nbItems;
            vm.reviews.page = page;
            offset = (page - 1) * nbItems;
            return fetchReviews(appId, nbItems, offset, vm.sortReviewsBy);
          }
        };
        fetchReviews(app.id, vm.reviews.nbItems, 0);
        updateAverageRating(vm.app.average_rating);
      }
      if (vm.areQuestionsEnabled) {
        vm.questions = {
          loading: true,
          nbItems: 100,
          page: 1,
          searchWord: '',
          pageChangedCb: function(appId, nbItems, page) {
            var offset;
            vm.questions.nbItems = nbItems;
            vm.questions.page = page;
            offset = (page - 1) * nbItems;
            return fetchQuestions(appId, nbItems, offset, searchWord);
          }
        };
        fetchQuestions(app.id, vm.questions.nbItems, 0);
      }
      vm.isLoading = false;
      vm.isTestimonialShown = function(testimonial) {
        return (testimonial.text != null) && testimonial.text.length > 0;
      };
      vm.canUserEditReview = function(review) {
        return (review.user_id === vm.userId) && (parseInt(review.edited_by_id) === review.user_id || !review.edited_by_id);
      };
      vm.cart = cart = {
        isOpen: false,
        bundle: {},
        config: {}
      };
      return cart.open = function() {
        cart.config.organizationId = MnoeOrganizations.selectedId;
        cart.bundle = {
          app_instances: [
            {
              app: {
                id: vm.app.id
              }
            }
          ]
        };
        return cart.isOpen = true;
      };
    };
    vm.openCreateReviewModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/create-review-modal.html',
        controller: 'CreateReviewModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static'
      });
      return modalInstance.result.then(function(response) {
        vm.reviews.totalItems++;
        vm.reviews.list.unshift(response.app_review);
        if (vm.reviews.list.length > vm.reviews.nbItems) {
          vm.reviews.list.pop();
        }
        return updateAverageRating(response.average_rating);
      });
    };
    vm.scrollToReviews = function() {
      $scope.active = 0;
      $location.hash('review-tabs');
      return $anchorScroll();
    };
    vm.openEditReviewModal = function(review, key) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/edit-review-modal.html',
        controller: 'EditReviewModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          review: review
        }
      });
      return modalInstance.result.then(function(response) {
        review = vm.reviews.list[key];
        review.description = response.app_feedback.description;
        review.rating = response.app_feedback.rating;
        review.edited = response.app_feedback.edited;
        review.edited_by_id = response.app_feedback.edited_by_id;
        return updateAverageRating(response.average_rating);
      });
    };
    vm.openDeleteReviewModal = function(review, key) {
      var modalOptions;
      modalOptions = {
        headerText: "mno_enterprise.templates.dashboard.marketplace.show.review.delete_modal_header",
        bodyText: "mno_enterprise.templates.dashboard.marketplace.show.review.delete_modal_body",
        closeButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.review.delete_modal_cancel',
        actionButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.review.delete_modal_delete',
        actionCb: function() {
          return MnoeMarketplace.deleteReview($stateParams.appId, review.id);
        },
        type: 'danger'
      };
      return MnoConfirm.showModal(modalOptions).then(function(response) {
        vm.reviews.list.splice(key, 1);
        return updateAverageRating(response.average_rating);
      });
    };
    vm.openCreateCommentModal = function(feedback, key) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/create-comment-modal.html',
        controller: 'CreateCommentModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          feedback: feedback
        }
      });
      return modalInstance.result.then(function(response) {
        return vm.reviews.list[key].comments.push(response.app_comment);
      });
    };
    vm.openEditCommentModal = function(comment, key, reviewKey) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/edit-modal.html',
        controller: 'EditCommentModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          object: comment
        }
      });
      return modalInstance.result.then(function(response) {
        comment = vm.reviews.list[reviewKey].comments[key];
        comment.description = response.app_comment.description;
        comment.edited = response.app_comment.edited;
        return comment.edited_by_id = response.app_comment.edited_by_id;
      });
    };
    vm.openDeleteCommentModal = function(comment, key, reviewKey) {
      var modalOptions;
      modalOptions = {
        headerText: "mno_enterprise.templates.dashboard.marketplace.show.comment.delete_modal_header",
        bodyText: "mno_enterprise.templates.dashboard.marketplace.show.comment.delete_modal_body",
        closeButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.comment.delete_modal_cancel',
        actionButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.comment.delete_modal_delete',
        actionCb: function() {
          return MnoeMarketplace.deleteComment($stateParams.appId, comment.id);
        },
        type: 'danger'
      };
      return MnoConfirm.showModal(modalOptions).then(function() {
        return vm.reviews.list[reviewKey].comments.splice(key, 1);
      });
    };
    vm.scrollToQuestions = function() {
      $scope.active = 1;
      $location.hash('review-tabs');
      return $anchorScroll();
    };
    vm.openCreateQuestionModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/create-question-modal.html',
        controller: 'CreateQuestionModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static'
      });
      return modalInstance.result.then(function(response) {
        return vm.questions.list.unshift(response.app_question);
      });
    };
    vm.openEditQuestionModal = function(question, key) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/edit-modal.html',
        controller: 'EditQuestionModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          question: question
        }
      });
      return modalInstance.result.then(function(response) {
        return vm.questions.list[key].description = response.app_question.description;
      });
    };
    vm.openDeleteQuestionModal = function(question, key) {
      var modalOptions;
      modalOptions = {
        headerText: "mno_enterprise.templates.dashboard.marketplace.show.question.delete_modal_header",
        bodyText: "mno_enterprise.templates.dashboard.marketplace.show.question.delete_modal_body",
        closeButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.question.delete_modal_cancel',
        actionButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.question.delete_modal_delete',
        actionCb: function() {
          return MnoeMarketplace.deleteQuestion($stateParams.appId, question.id);
        },
        type: 'danger'
      };
      return MnoConfirm.showModal(modalOptions).then(function() {
        return vm.questions.list.splice(key, 1);
      });
    };
    vm.openCreateAnswerModal = function(question, key) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/create-answer-modal.html',
        controller: 'CreateAnswerModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          question: question
        }
      });
      return modalInstance.result.then(function(response) {
        return vm.questions.list[key].answers.push(response.app_answer);
      });
    };
    vm.openEditAnswerModal = function(answer, key, questionKey) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/edit-modal.html',
        controller: 'EditAnswerModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          object: answer
        }
      });
      return modalInstance.result.then(function(response) {
        return vm.questions.list[questionKey].answers[key].description = response.app_answer.description;
      });
    };
    vm.openDeleteAnswerModal = function(answer, key, questionKey) {
      var modalOptions;
      modalOptions = {
        headerText: "mno_enterprise.templates.dashboard.marketplace.show.answer.delete_modal_header",
        bodyText: "mno_enterprise.templates.dashboard.marketplace.show.answer.delete_modal_body",
        closeButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.answer.delete_modal_cancel',
        actionButtonText: 'mno_enterprise.templates.dashboard.marketplace.show.answer.delete_modal_delete',
        actionCb: function() {
          return MnoeMarketplace.deleteAnswer($stateParams.appId, answer.id);
        },
        type: 'danger'
      };
      return MnoConfirm.showModal(modalOptions).then(function() {
        return vm.questions.list[questionKey].answers.splice(key, 1);
      });
    };
    vm.showHistory = function(review) {
      return $uibModal.open({
        templateUrl: 'app/views/marketplace/modals/review-history-modal.html',
        controller: 'ReviewHistoryModalCtrl',
        controllerAs: 'vm',
        size: 'lg',
        windowClass: 'inverse',
        backdrop: 'static',
        resolve: {
          review: review
        }
      });
    };
    vm.searchQuestion = function() {
      return fetchQuestions(vm.app.id, vm.questions.nbItems, vm.questions.offset, vm.questions.searchWord);
    };
    vm.orderFeedbacks = function() {
      return fetchReviews(vm.app.id, vm.reviews.nbItems, 0, vm.sortReviewsBy);
    };
    fetchReviews = function(appId, limit, offset, sort) {
      if (sort == null) {
        sort = 'created_at.desc';
      }
      vm.reviews.loading = true;
      return MnoeMarketplace.getReviews(appId, limit, offset, sort).then(function(response) {
        var ref;
        vm.reviews.totalItems = response.headers('x-total-count');
        vm.reviews.list = response.data;
        return vm.anyReviews = (ref = vm.reviews.list.length !== 0) != null ? ref : {
          "true": false
        };
      })["finally"](function() {
        return vm.reviews.loading = false;
      });
    };
    fetchQuestions = function(appId, limit, offset, search) {
      if (search == null) {
        search = '';
      }
      vm.questions.loading = true;
      return MnoeMarketplace.getQuestions(appId, limit, offset, search).then(function(response) {
        var ref;
        vm.questions.list = response.data;
        return vm.anyQuestions = (ref = vm.questions.list.length !== 0) != null ? ref : {
          "true": false
        };
      })["finally"](function() {
        return vm.questions.loading = false;
      });
    };
    updateAverageRating = function(rating) {
      vm.averageRating = rating != null ? parseFloat(rating).toFixed(1) : null;
      return vm.isRateDisplayed = !!vm.averageRating;
    };
    $scope.$watch(MnoeOrganizations.getSelectedId, function(val) {
      if (val != null) {
        vm.isLoading = true;
        return $q.all({
          marketplace: MnoeMarketplace.getApps(),
          appInstances: MnoeAppInstances.getAppInstances(),
          products: MnoeProvisioning.getProducts()
        }).then(function(response) {
          var app, appId, appInstance, appInstances, apps, product, products;
          apps = response.marketplace.apps;
          appInstances = response.appInstances;
          products = response.products.products;
          appId = $stateParams.appId;
          app = _.findWhere(apps, {
            nid: appId
          });
          app || (app = _.findWhere(apps, {
            id: appId
          }));
          if (app == null) {
            $state.go('home.marketplace');
          }
          appInstance = _.find(appInstances, {
            app_nid: app.nid
          });
          product = _.find(products, {
            nid: app.nid
          });
          return vm.initialize(app, appInstance, product);
        });
      }
    });
  }]);

}).call(this);
