(function() {
  angular.module('mnoEnterpriseAngular').controller('CreateQuestionModalCtrl', ["$log", "$stateParams", "$uibModalInstance", "toastr", "Utilities", "MnoeMarketplace", "MnoeOrganizations", function($log, $stateParams, $uibModalInstance, toastr, Utilities, MnoeMarketplace, MnoeOrganizations) {
    var vm;
    vm = this;
    vm.modal = {
      model: {}
    };
    vm.commentMaxLenght = 500;
    vm.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.modal.proceed = function() {
      var app_review;
      vm.modal.isLoading = true;
      app_review = {
        description: vm.modal.model.description,
        organization_id: MnoeOrganizations.getSelectedId()
      };
      return MnoeMarketplace.addAppQuestion($stateParams.appId, app_review).then(function(response) {
        toastr.success('mno_enterprise.templates.dashboard.marketplace.show.success_toastr_question');
        return $uibModalInstance.close(response);
      }, function(errors) {
        $log.error(errors);
        toastr.error('mno_enterprise.templates.dashboard.marketplace.show.error_toastr_question');
        return Utilities.processRailsError(errors);
      })["finally"](function() {
        return vm.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
