(function() {
  var DashboardOrganizationInvoicesCtrl;

  DashboardOrganizationInvoicesCtrl = ["$scope", "$window", "MnoeOrganizations", "MnoeConfig", function($scope, $window, MnoeOrganizations, MnoeConfig) {
    'ngInject';
    $scope.isLoading = true;
    $scope.invoices = [];
    $scope.payment_enabled = MnoeConfig.isPaymentEnabled();
    return $scope.$watch(MnoeOrganizations.getSelected, function(val) {
      if (val != null) {
        return $scope.invoices = MnoeOrganizations.selected.invoices;
      }
    });
  }];

  angular.module('mnoEnterpriseAngular').directive('dashboardOrganizationInvoices', function() {
    return {
      restrict: 'A',
      scope: {},
      templateUrl: 'app/views/company/invoices/organization-invoices.html',
      controller: DashboardOrganizationInvoicesCtrl
    };
  });

}).call(this);
