(function() {
  angular.module('mnoEnterpriseAngular').service('MnoeProvisioning', ["$q", "$log", "MnoeApiSvc", "MnoeOrganizations", function($q, $log, MnoeApiSvc, MnoeOrganizations) {
    var _self, defaultSubscription, productsApi, productsPromise, productsResponse, subscription, subscriptionsApi;
    _self = this;
    subscriptionsApi = function(id) {
      return MnoeApiSvc.one('/organizations', id).all('subscriptions');
    };
    productsApi = MnoeApiSvc.oneUrl('/products');
    productsPromise = null;
    productsResponse = null;
    subscription = {};
    defaultSubscription = {
      id: null,
      product: null,
      product_pricing: null,
      custom_data: {}
    };
    this.getProducts = function() {
      if (productsPromise != null) {
        return productsPromise;
      }
      return productsPromise = productsApi.get().then(function(response) {
        productsResponse = response.plain();
        return response;
      });
    };
    this.findProduct = function(arg) {
      var id, nid, ref, ref1;
      id = (ref = arg.id) != null ? ref : null, nid = (ref1 = arg.nid) != null ? ref1 : null;
      return _self.getProducts().then(function() {
        return _.find(productsResponse.products, function(a) {
          return a.id === id || a.nid === nid;
        });
      });
    };
    this.setSubscription = function(s) {
      return subscription = s;
    };
    this.getSubscription = function() {
      return subscription;
    };
    this.initSubscription = function(arg) {
      var deferred, productNid, ref, ref1, subscriptionId;
      productNid = (ref = arg.productNid) != null ? ref : null, subscriptionId = (ref1 = arg.subscriptionId) != null ? ref1 : null;
      deferred = $q.defer();
      if (productNid != null) {
        angular.copy(defaultSubscription, subscription);
        deferred.resolve(subscription);
      } else if (subscriptionId != null) {
        _self.fetchSubscription(subscriptionId).then(function(response) {
          angular.copy(response, subscription);
          return deferred.resolve(subscription);
        });
      } else {
        deferred.resolve(subscription);
      }
      return deferred.promise;
    };
    this.createSubscription = function(s) {
      var deferred;
      deferred = $q.defer();
      MnoeOrganizations.get().then(function(response) {
        return subscriptionsApi(response.organization.id).post({
          subscription: {
            product_pricing_id: s.product_pricing.id,
            custom_data: s.custom_data
          }
        }).then(function(response) {
          return deferred.resolve(response);
        });
      });
      return deferred.promise;
    };
    this.updateSubscription = function(s) {
      var deferred;
      deferred = $q.defer();
      MnoeOrganizations.get().then(function(response) {
        return subscription.patch({
          subscription: {
            product_pricing_id: s.product_pricing.id,
            custom_data: s.custom_data
          }
        }).then(function(response) {
          return deferred.resolve(response);
        });
      });
      return deferred.promise;
    };
    this.saveSubscription = function(subscription) {
      if (!subscription.id) {
        return _self.createSubscription(subscription);
      } else {
        return _self.updateSubscription(subscription);
      }
    };
    this.fetchSubscription = function(id) {
      var deferred;
      deferred = $q.defer();
      MnoeOrganizations.get().then(function(response) {
        return MnoeApiSvc.one('/organizations', response.organization.id).one('subscriptions', id).get().then(function(response) {
          return deferred.resolve(response);
        });
      });
      return deferred.promise;
    };
    this.getSubscriptions = function() {
      var deferred;
      deferred = $q.defer();
      MnoeOrganizations.get().then(function(response) {
        return subscriptionsApi(response.organization.id).getList().then(function(response) {
          return deferred.resolve(response);
        });
      });
      return deferred.promise;
    };
  }]);

}).call(this);
