(function() {
  angular.module('mnoEnterpriseAngular').controller('ImpacController', ["$scope", "$state", "ImpacDashboardsSvc", "MnoeCurrentUser", "MnoeOrganizations", "MnoeConfig", function($scope, $state, ImpacDashboardsSvc, MnoeCurrentUser, MnoeOrganizations, MnoeConfig) {
    'ngInject';
    var vm;
    vm = this;
    vm.isImpacShown = false;
    vm.isDockEnabled = MnoeConfig.isDockEnabled();
    $scope.$watch(MnoeOrganizations.getSelectedId, function(newValue, oldValue) {
      if (newValue != null) {
        return MnoeCurrentUser.get().then(function(response) {
          var selectedOrg;
          selectedOrg = _.find(response.organizations, {
            id: newValue
          });
          if (MnoeOrganizations.role.atLeastAdmin(selectedOrg.current_user_role)) {
            vm.isImpacShown = true;
            if ((newValue != null) && (oldValue != null) && newValue !== oldValue) {
              return ImpacDashboardsSvc.reload(true);
            }
          } else {
            return $state.go('home.apps');
          }
        });
      }
    });
  }]);

}).call(this);
