(function() {
  angular.module('mnoEnterpriseAngular').controller('ReviewHistoryModalCtrl', ["$uibModalInstance", "MnoeMarketplace", "review", function($uibModalInstance, MnoeMarketplace, review) {
    var vm;
    vm = this;
    vm.review = review;
    vm.versions = [];
    vm.isLoading = true;
    vm.closeModal = function() {
      return $uibModalInstance.dismiss('close');
    };
    MnoeMarketplace.getReview(review.app_id, review.id).then(function(response) {
      vm.versions = response.versions;
      return vm.isLoading = false;
    });
  }]);

}).call(this);
