(function() {
  angular.module('mnoEnterpriseAngular').controller('EditReviewModalCtrl', ["$log", "$stateParams", "$uibModalInstance", "toastr", "Utilities", "MnoeMarketplace", "review", function($log, $stateParams, $uibModalInstance, toastr, Utilities, MnoeMarketplace, review) {
    var vm;
    vm = this;
    vm.modal = {
      model: {}
    };
    vm.appRating = review.rating;
    vm.commentMaxLenght = 500;
    vm.modal.model.description = review.description;
    vm.modal.cancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.modal.proceed = function() {
      var app_feedback;
      vm.modal.isLoading = true;
      app_feedback = {
        description: vm.modal.model.description,
        rating: vm.appRating
      };
      return MnoeMarketplace.editReview($stateParams.appId, review.id, app_feedback).then(function(response) {
        toastr.success('mno_enterprise.templates.dashboard.marketplace.show.success_toastr_2');
        return $uibModalInstance.close(response);
      }, function(errors) {
        $log.error(errors);
        toastr.error('mno_enterprise.templates.dashboard.marketplace.show.error_toastr');
        return Utilities.processRailsError(errors);
      })["finally"](function() {
        return vm.modal.isLoading = false;
      });
    };
  }]);

}).call(this);
