(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  angular.module('mnoEnterpriseAngular').component('mnoAppOnboardingBtn', {
    bindings: {
      appInstance: '='
    },
    templateUrl: 'app/components/mno-app-onboarding-btn/mno-app-onboarding-btn.html',
    controller: ["$q", "$state", "$window", "$uibModal", "toastr", "MnoeMarketplace", "MnoeCurrentUser", "MnoeOrganizations", "MnoeAppInstances", function($q, $state, $window, $uibModal, toastr, MnoeMarketplace, MnoeCurrentUser, MnoeOrganizations, MnoeAppInstances) {
      var appInstallationStatus, ctrl;
      ctrl = this;
      ctrl.launchAppInstance = function() {
        return $window.open("/mnoe/launch/" + ctrl.appInstance.uid, '_blank');
      };
      ctrl.launchOAuthConnectPath = function() {
        $window.location.href = MnoeAppInstances.oAuthConnectPath(ctrl.appInstance);
        return true;
      };
      ctrl.connectAppInstance = function() {
        var modalInfo;
        switch (ctrl.appInstance.app_nid) {
          case "xero":
            modalInfo = {
              template: "app/views/apps/modals/app-connect-modal-xero.html",
              controller: 'DashboardAppConnectXeroModalCtrl'
            };
            break;
          case "myob":
            modalInfo = {
              template: "app/views/apps/modals/app-connect-modal-myob.html",
              controller: 'DashboardAppConnectMyobModalCtrl'
            };
            break;
          default:
            ctrl.launchOAuthConnectPath();
        }
        return $uibModal.open({
          templateUrl: modalInfo.template,
          controller: modalInfo.controller,
          resolve: {
            app: ctrl.appInstance
          }
        });
      };
      appInstallationStatus = function() {
        if (ctrl.conflictingApp) {
          return "CONFLICT";
        } else if (ctrl.appInstance.add_on) {
          return "ADD_ON";
        } else {
          return MnoeAppInstances.installStatus(ctrl.appInstance);
        }
      };
      ctrl.init = function() {
        var appInstancesPromise;
        ctrl.isLoadingAppInstances = true;
        appInstancesPromise = MnoeAppInstances.getAppInstances().then(function(response) {
          ctrl.appInstance.status = appInstallationStatus();
          ctrl.isLoadingAppInstances = false;
          return response;
        });
        return $q.all({
          marketplace: MnoeMarketplace.getApps(),
          appInstances: appInstancesPromise
        }).then(function(response) {
          var appInstances, apps, installedApps, names, nids;
          apps = response.marketplace.apps;
          appInstances = response.appInstances;
          ctrl.app = _.find(apps, {
            nid: ctrl.appInstance.app_nid
          });
          nids = _.compact(_.map(appInstances, function(a) {
            return a.app_nid;
          }));
          installedApps = _.filter(apps, function(a) {
            var ref;
            return ref = a.nid, indexOf.call(nids, ref) >= 0;
          });
          if (ctrl.app.subcategories) {
            names = _.map(ctrl.app.subcategories, 'name');
            return ctrl.conflictingApp = _.find(installedApps, function(app) {
              return _.find(app.subcategories, function(subCategory) {
                var ref;
                return !subCategory.multi_instantiable && (ref = subCategory.name, indexOf.call(names, ref) >= 0);
              });
            });
          }
        })["finally"](function() {
          return ctrl.isLoadingAppInstances = false;
        });
      };
      ctrl.init();
    }]
  });

}).call(this);
