(function() {
  angular.module('mnoEnterpriseAngular').service('themeEditorSvc', ["$log", "$http", function($log, $http) {
    var _self, getLessVars;
    _self = this;
    this.saveTheme = function(theme, opts) {
      var body, uploadUrl;
      if (opts == null) {
        opts = {};
      }
      uploadUrl = '/mnoe/jpi/v1/admin/theme/save';
      body = {
        theme: theme,
        publish: !!opts.publish
      };
      return $http.post(uploadUrl, angular.toJson(body), {
        transformRequest: angular.identity
      });
    };
    this.saveLogo = function(file) {
      var data, opts, uploadUrl;
      uploadUrl = '/mnoe/jpi/v1/admin/theme/logo';
      opts = {
        transformRequest: angular.identity,
        headers: {
          'Content-Type': void 0
        }
      };
      data = new FormData();
      data.append('logo', file);
      return $http.put(uploadUrl, data, opts);
    };
    this.resetToPublishedTheme = function() {
      var resetUrl;
      resetUrl = '/mnoe/jpi/v1/admin/theme/reset';
      return $http.post(resetUrl, '');
    };
    this.getTheme = function() {
      $log.debug('Loading custom theme');
      return $http.get('styles/theme-previewer.less').then(function(response) {
        var data;
        data = response.data;
        return getLessVars(data);
      });
    };
    getLessVars = function(lessStr) {
      var i, j, keyVar, len, lessVars, line, lines, value;
      lines = lessStr.split('\n');
      lessVars = {};
      for (j = 0, len = lines.length; j < len; j++) {
        line = lines[j];
        if (line.indexOf('@') === 0) {
          i = line.indexOf(':');
          keyVar = line.slice(0, i);
          value = line.trim().slice(i + 1, -1).trim();
          lessVars[keyVar] = value;
        }
      }
      return lessVars;
    };
    return this;
  }]);

}).call(this);
